<template>
    <div>
        <h3 class="margin-b-14">亲属信息
            <ocr-button itemid="consortIDPositive" ocrtype="1" @on-ocr-result="setOcrRes">上传身份证部分免填写
            </ocr-button>
        </h3>
        <Form ref="formModel" :model="formModel" :rules="relativeRule" :label-width="120">
            <Row>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="与申请人关系" prop="relativeType">
                        <i-select v-model.sync="formModel.relativeType" placeholder="请选择"
                                  :disabled="shipDisabled"
                                  @on-change="val=>{ handleChange(val,'relative.relativeType')}">
                            <i-option v-for="(item,index) in DICT_relativeship" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="姓名" prop="relativeName">
                        <Input clearable v-model="formModel.relativeName" placeholder="请输入" v="relative.relativeName"
                               @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="证件类型" prop="relativeCertificateType">
                        <i-select v-model.sync="formModel.relativeCertificateType"
                                  placeholder="请选择"
                                  @on-change="val=>{ handleChange(val,'relative.relativeCertificateType')}">
                            <i-option v-for="(item,index) in dp['certType']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="证件号码" prop="relativeCertificateNo" :rules="createCertNoRule()">
                        <Input clearable v-model="formModel.relativeCertificateNo" placeholder="请输入"
                               v="relative.relativeCertificateNo" @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="手机号" prop="relativePhone">
                        <Input clearable v-model="formModel.relativePhone" placeholder="请输入" v="relative.relativePhone"
                               @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="税后年薪（万元）" prop="relativeAnnualSalaryAfterTax">
                        <InputNumber clearable v-model="formModel.relativeAnnualSalaryAfterTax" placeholder="请输入"
                                     v="relative.relativeAnnualSalaryAfterTax" @on-blur="handleBlur"
                                     @on-clear="handleBlur"></InputNumber>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="16">
                    <FormItem label="单位名称" prop="relativeCompanyName">
                        <Input clearable v-model="formModel.relativeCompanyName" placeholder="请输入"
                               v="relative.relativeCompanyName" @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="16">
                    <FormItem label="单位所在地省市区" class="hascol-form-item">
                        <region-picker type="3" v-model="formModel.relativeCompany_Region"
                                       regionlabel="单位所在地" ref="regionRef1"
                                       @on-change="changeRegion('relativeCompany','relative.relativeCompany_Region')"></region-picker>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="16">
                    <FormItem label="单位详细地址" prop="relativeCompanyAddress">
                        <Input v-model="formModel.relativeCompanyAddress" placeholder="请输入" clearable
                               v="relative.relativeCompanyAddress" @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
            </Row>
        </Form>
    </div>
</template>
<script>
    /**
     * @Description: 个人申请人的亲属表单
     */
    import base from './js/custbase';
    import rules from './js/custRules';
    import {validators, required} from "@/utils/validateRules";

    export default {
        name: 'orderRelativeForm',
        mixins: [base],
        props: {},
        components: {},
        data() {
            return {
                relativeRule: rules.relativeRule,
                targetObj: {},
                shipDisabled: false,
            }
        },
        computed: {
            //返回亲属model
            formModel() {
                return this.orderData.cust.relative;
            },
            personalInfo() {
                return this.orderData.cust.personal;
            },
            DICT_relativeship() {
                //1已婚 0未婚
                if (this.personalInfo && this.personalInfo.marriage === '1') {
                    if (this.formModel.relativeType == '3') this.shipDisabled = true;
                    else {
                        this.formModel.relativeType = '';
                        this.shipDisabled = false;
                    }
                    return (this.dp['relativeRelationship'] || []).filter((item) => item.dictCode == '3');
                } else if (this.personalInfo && this.personalInfo.marriage === '0') {
                    this.shipDisabled = false;
                    return (this.dp['relativeRelationship'] || []).filter((item) => item.dictCode != '3');
                } else {
                    this.shipDisabled = false;
                    return this.dp['relativeRelationship'];
                }
            }
        },
        mounted() {
            this.queryDictData(['certType', 'relativeRelationship']);
            this.$nextTick(() => {
                this.todoRecount();
            })
        },
        methods: {
            //证件号码验证规则
            createCertNoRule() {
                var rule = [required('证件号码')];
                if (this.formModel.relativeCertificateType == '1' || this.formModel.relativeCertificateType == '7') {
                    rule.push({
                        validator: validators.certNo,
                        trigger: 'change'
                    })
                }
                return rule;
            },
            //校验当前担保人，可由父页面调用
            validate(cb) {
                let flag = true;
                let refName = ['formModel', 'regionRef1'];
                let arr = [];
                refName.forEach(ref => {
                    arr.push(new Promise((resolve) => {
                        if (this.$refs[ref]) this.$refs[ref].validate((valid) => {
                            resolve(valid);
                        });
                        else resolve(true)
                    }))
                })
                Promise.all(arr).then(function (result) {
                    flag = _.every(result);
                    cb && cb(flag);
                });
            },
            //输入框失去焦点
            handleBlur(e) {
                let handleField = e.target.parentNode.getAttribute('v') || e.target.parentNode.parentNode.getAttribute('v');
                //判断值是否改变
                if (!this.checkFieldIsChange(handleField)) return false;
                this.$nextTick(() => {
                    this.cacheData('relative');
                    this.todoRecount();
                })
            },
            //下拉框改变值
            handleChange(val, handleField, regionChange) {
                //判断值是否改变
                if (!this.checkFieldIsChange(handleField) && !regionChange) return false;
                this.$nextTick(() => {
                    if (handleField == "relative.relativeCertificateType" && this.formModel.relativeCertificateNo) {//证件类型切换时，校验
                        this.$refs['formModel'].validateField('relativeCertificateNo');
                    }
                    this.cacheData('relative');
                    this.todoRecount();
                })
            },
            //地区改变选择时，store赋值给有效字段
            changeRegion(field, handleField) {
                this.setChangeRegion({
                    module: 'relative',
                    field: field,
                })
                this.handleChange(field, handleField, true)
            },
            //上传身份证部分  免填写
            relativeUploadIDCard(inputname) {
                this.$refs[inputname].value = ''; //先清空文件选择框的值
                this.$refs[inputname].click(); //输入框点击
            },
            //识别结果回填
            setOcrRes(ocrRes) {
                var hasChange = this.formModel.relativeCertificateNo != ocrRes.idcard ||
                    this.formModel.relativeName != ocrRes.name ||
                    this.formModel.relativeCertificateType != "1";
                if (!hasChange) return false;
                this.formModel.relativeCertificateNo = ocrRes.idcard;//身份证号
                this.formModel.relativeName = ocrRes.name;//姓名
                this.formModel.relativeCertificateType = "1";//身份证类型
                this.$nextTick(() => {
                    this.cacheData('relative');
                    this.todoRecount()
                })
            },
        },
        watch: {}
    }
</script>



