<template>
    <div>
        <h3 class="margin-b-14">企业信息
            <ocr-button itemid="applicantBusinessLicence" ocrtype="2" @on-ocr-result="setOcrRes">上传营业执照部分免填写
            </ocr-button>
            <ocr-button itemid="applicantOrderIdPositive" ocrtype="1" @on-ocr-result="setOcrRes">
                上传申请人身份证部分免填写
            </ocr-button>
        </h3>
        <Form ref="formModel" :model="formModel" :rules="enterpriseRule" :label-width="120">
            <Row>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="订单申请类型" prop="orderApplicationType">
                        <i-select type="text" v-model="formModel.orderApplicationType" placeholder="请选择"
                                  v="enterprise.orderApplicationType"
                                  @on-change="val=>{ handleChange(val,'enterprise.orderApplicationType')}">
                            <i-option v-for="(item,index) in dp['applyType']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="企业名称" prop="enterpriseName">
                        <Input clearable v-model="formModel.enterpriseName" placeholder="请输入"
                               v="enterprise.enterpriseName"
                               @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="统一社会信用代码" prop="unifiedSocialCreditCode">
                        <Input clearable v-model="formModel.unifiedSocialCreditCode" placeholder="请输入"
                               v="enterprise.unifiedSocialCreditCode"
                               @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="组织机构代码" prop="organizationCode">
                        <Input clearable v-model="formModel.organizationCode" placeholder="请输入"
                               v="enterprise.organizationCode"
                               @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="组织机构类别" prop="organizationCategory">
                        <i-select type="text" v-model="formModel.organizationCategory" placeholder="请选择"
                                  @on-change="val=>{ handleChange(val,'enterprise.organizationCategory')}">
                            <i-option v-for="(item,index) in DICT_orgType" :value="item.value" :key="item.value">
                                {{item.name}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="组织机构类别细分" prop="organizationCategoryDetail">
                        <i-select type="text" v-model="formModel.organizationCategoryDetail"
                                  placeholder="请选择" :disabled="!formModel.organizationCategory"
                                  @on-change="val=>{ handleChange(val,'enterprise.organizationCategoryDetail')}">
                            <i-option
                                    v-for="(item,index) in orgList.filter(item => item.parent == formModel.organizationCategory)"
                                    :value="item.value"
                                    :key="item.value">
                                {{item.name}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="纳税人识别号" prop="taxpayerIdentificationNum">
                        <Input clearable v-model="formModel.taxpayerIdentificationNum" placeholder="请输入"
                               v="enterprise.taxpayerIdentificationNum"
                               @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="中征码" prop="eigenCode" :rules="eigenCodeRule">
                        <Input clearable v-model="formModel.eigenCode" placeholder="请输入" v="enterprise.eigenCode"
                               :disabled="formModel.orderApplicationType!='2'"
                               @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="国别" prop="country">
                        <i-select type="text" v-model="formModel.country"
                                  placeholder="请选择"
                                  @on-change="val=>{ handleChange(val,'enterprise.country')}">
                            <i-option v-for="(item,index) in dp['country']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <!--<i-col :xs="24" :sm="12" :md="8">-->
                <!--<FormItem label="国别" prop="country">-->
                <!--<div style="position: relative;width:100%;"-->
                <!--v-clickoutside:click="hideCountryDrop">-->
                <!--<i-select multiple style="display:none"-->
                <!--v-model="formModel.country"-->
                <!--class="ivu-select-visible"></i-select>-->
                <!--<i-input type="text" readonly-->
                <!--:value="getCountryText(formModel.country)"-->
                <!--placeholder="请选择"-->
                <!--@click.native.stop="showCountryDrop=!showCountryDrop">-->
                <!--</i-input>-->
                <!--<i class="ivu-icon ivu-icon-ios-arrow-down ivu-select-arrow"-->
                <!--style="position:absolute;z-index:9"-->
                <!--:style="showCountryDrop?{transform: 'rotate(180deg)'}:{}"-->
                <!--@click.stop="showCountryDrop=!showCountryDrop"></i>-->
                <!--<div class="ivu-select-dropdown" v-show="showCountryDrop">-->
                <!--<ul class="ivu-select-dropdown-list">-->
                <!--<li v-for="item in dp['country']" class="ivu-select-item"-->
                <!--:class="item.dictCode==formModel.country?'ivu-select-item-selected':''"-->
                <!--@click.stop="selectCountry(item)">-->
                <!--{{item.dictName}}-->
                <!--</li>-->
                <!--</ul>-->
                <!--</div>-->
                <!--</div>-->
                <!--</FormItem>-->
                <!--</i-col>-->

                <i-col :xs="24" :sm="12" :md="8">
                    <Form-item label="是否事业单位" prop="isInstitution">
                        <RadioGroup v-model="formModel.isInstitution"
                                    @on-change="val=>{ handleChange(val,'enterprise.isInstitution')}">
                            <Radio v-for="(item,index) in dp['whether']" :label="item.dictCode"
                                   :key="item.dictCode">
                                <span>{{item.dictName}}</span>
                            </Radio>
                        </RadioGroup>
                    </Form-item>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="成立日期" prop="establishDate">
                        <DatePicker v-model="formModel.establishDate" type="date"
                                    :options="currentDateOptions"
                                    :editable="false" placeholder="请选择"
                                    @on-change="val=>{ handleChange(val,'enterprise.establishDate')}"></DatePicker>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="企业规模" prop="enterpriseScale">
                        <i-select type="text" v-model="formModel.enterpriseScale" placeholder="请选择"
                                  @on-change="val=>{ handleChange(val,'enterprise.enterpriseScale')}">
                            <i-option v-for="(item,index) in dp['firmScale']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="证书是否长期" prop="isLongCertificate">
                        <RadioGroup v-model="formModel.isLongCertificate"
                                    @on-change="val=>{ handleChange(val,'enterprise.isLongCertificate')}">
                            <Radio v-for="(item,index) in dp['whether']" :label="item.dictCode"
                                   :key="item.dictCode">
                                <span>{{item.dictName}}</span>
                            </Radio>
                        </RadioGroup>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="证书到期日期" prop="certificateExpiryDate" :rules="certificateExpiryDateRule">
                        <DatePicker v-model="formModel.certificateExpiryDate" type="date" :editable="false"
                                    placeholder="请选择"
                                    :disabled="formModel.isLongCertificate=='1'"
                                    @on-change="val=>{ handleChange(val,'enterprise.certificateExpiryDate')}"></DatePicker>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="机构状态" prop="organizationStatus">
                        <i-select type="text" v-model="formModel.organizationStatus" placeholder="请选择"
                                  @on-change="val=>{ handleChange(val,'enterprise.organizationStatus')}">
                            <i-option v-for="(item,index) in dp['orgStatus']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="注册资本币种" prop="registeredCapitalCurrency">
                        <i-select type="text" v-model="formModel.registeredCapitalCurrency"
                                  placeholder="请选择"
                                  @on-change="val=>{ handleChange(val,'enterprise.registeredCapitalCurrency')}">
                            <i-option v-for="(item,index) in dp['currency']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <!--<i-col :xs="24" :sm="12" :md="8">-->
                <!--<FormItem label="注册资本币种" prop="registeredCapitalCurrency">-->
                <!--<div style="position: relative;width:100%;"-->
                <!--v-clickoutside:click="hideCurrencyDrop">-->
                <!--<i-select multiple style="display:none"-->
                <!--v-model="formModel.registeredCapitalCurrency"-->
                <!--class="ivu-select-visible"></i-select>-->
                <!--<i-input type="text" readonly-->
                <!--:value="getCurrencyText(formModel.registeredCapitalCurrency)"-->
                <!--placeholder="请选择"-->
                <!--@click.native.stop="showCurrencyDrop=!showCurrencyDrop">-->
                <!--</i-input>-->
                <!--<i class="ivu-icon ivu-icon-ios-arrow-down ivu-select-arrow"-->
                <!--style="position:absolute;z-index:9"-->
                <!--:style="showCurrencyDrop?{transform: 'rotate(180deg)'}:{}"-->
                <!--@click.stop="showCurrencyDrop=!showCurrencyDrop"></i>-->
                <!--<div class="ivu-select-dropdown" v-show="showCurrencyDrop">-->
                <!--<ul class="ivu-select-dropdown-list">-->
                <!--<li v-for="item in dp['currency']" class="ivu-select-item"-->
                <!--:class="item.dictCode==formModel.registeredCapitalCurrency?'ivu-select-item-selected':''"-->
                <!--@click.stop="selectCurrency(item)">-->
                <!--{{item.dictName}}-->
                <!--</li>-->
                <!--</ul>-->
                <!--</div>-->
                <!--</div>-->
                <!--</FormItem>-->
                <!--</i-col>-->
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="注册资本(万)" prop="registeredCapital">
                        <InputNumber clearable v-model="formModel.registeredCapital" placeholder="请输入"
                                     v="enterprise.registeredCapital"
                                     @on-blur="handleBlur" @on-clear="handleBlur"></InputNumber>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="经济类型" prop="economicType">
                        <i-select type="text" v-model="formModel.economicType" placeholder="请选择"
                                  @on-change="val=>{ handleChange(val,'enterprise.economicType')}">
                            <i-option v-for="(item,index) in dp['economicType']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="经济行业大类" prop="economicIndustryLargeClass">
                        <i-select type="text" v-model="formModel.economicIndustryLargeClass"
                                  placeholder="请选择"
                                  @on-change="val=>{ handleChange(val,'enterprise.economicIndustryLargeClass')}">
                            <i-option v-for="(item,index) in dp['industryLageClass']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="经济行业小类" prop="economicIndustrySmallClass">
                        <i-select type="text" v-model="formModel.economicIndustrySmallClass"
                                  placeholder="请选择" :disabled="!formModel.economicIndustryLargeClass"
                                  @on-change="val=>{ handleChange(val,'enterprise.economicIndustrySmallClass')}">
                            <i-option v-for="(item,index) in dp['industrySmallClass']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="16">
                    <FormItem label="企业注册地省市区" class="hascol-form-item">
                        <region-picker type="3" v-model="formModel.enterpriseRegistration_Region"
                                       regionlabel="企业注册地" ref="regionRef1"
                                       @on-change="changeRegion('enterpriseRegistration','enterprise.enterpriseRegistration_Region')"></region-picker>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="16">
                    <FormItem label="企业注册地详细地址" prop="enterpriseRegistrationAddress">
                        <Input clearable
                               v-model="formModel.enterpriseRegistrationAddress" placeholder="请输入"
                               v="enterprise.enterpriseRegistrationAddress"
                               @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="16">
                    <FormItem label="企业经营地省市区" class="hascol-form-item">
                        <region-picker type="3" v-model="formModel.enterpriseManagement_Region"
                                       regionlabel="企业经营地" ref="regionRef2"
                                       @on-change="changeRegion('enterpriseManagement','enterprise.enterpriseManagement_Region')"></region-picker>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="16">
                    <FormItem label="企业经营地详细地址" prop="enterpriseManagementAddress">
                        <Input clearable
                               v-model="formModel.enterpriseManagementAddress" placeholder="请输入"
                               v="enterprise.enterpriseManagementAddress"
                               @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="24" :md="24">
                    <FormItem label="经营（业务）范围" prop="enterpriseBusinessScope">
                        <Input clearable type="textarea"
                               v-model="formModel.enterpriseBusinessScope" :rows="2" placeholder="请输入"
                               v="enterprise.enterpriseBusinessScope"
                               @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="法人姓名" prop="corporationName">
                        <Input clearable v-model="formModel.corporationName" placeholder="请输入"
                               v="enterprise.corporationName"
                               @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="法人证件类型" prop="corporationCertificateType">
                        <i-select type="text" v-model="formModel.corporationCertificateType"
                                  placeholder="请选择"
                                  @on-change="val=>{ handleChange(val,'enterprise.corporationCertificateType')}">
                            <i-option v-for="(item,index) in dp['frCertType']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="法人证件号码" prop="corporationCertificateNo" :rules="createCertNoRule()">
                        <Input clearable v-model="formModel.corporationCertificateNo" placeholder="请输入"
                               v="enterprise.corporationCertificateNo"
                               @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="法人手机号" prop="corporationPhone">
                        <Input clearable v-model="formModel.corporationPhone" placeholder="请输入"
                               v="enterprise.corporationPhone"
                               @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="法人持股比例(%)" prop="corporationShareholdingRatio">
                        <InputNumber clearable
                                     v-model="formModel.corporationShareholdingRatio" placeholder="请输入"
                                     v="enterprise.corporationShareholdingRatio"
                                     @on-blur="handleBlur" @on-clear="handleBlur"></InputNumber>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="企业联系人电话" prop="enterpriseContactPhone">
                        <Input clearable v-model="formModel.enterpriseContactPhone" placeholder="请输入"
                               v="enterprise.enterpriseContactPhone"
                               @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
            </Row>
        </Form>
    </div>
</template>
<script>
    /**
     * @Description: 企业表单
     */
    import base from './js/custbase';
    import rules from './js/custRules';
    import {yx} from '@/utils/helper';
    import {validators, required} from "@/utils/validateRules";

    export default {
        name: 'orderEnterpriseForm',
        mixins: [base],
        props: [],
        components: {},
        data() {
            return {
                orgList: [],//组织机构所有列表
                DICT_orgType: [],//组织机构列表
                DICT_orgSmallType: [],//组织机构细分列表
                enterpriseRule: rules.enterpriseRule,
                showCountryDrop: false,//显示国别下拉
                showCurrencyDrop: false,//显示币种下拉
                //日期禁止选择当前日期之前
                currentDateOptions: {
                    disabledDate(date) {
                        return date && date.valueOf() > new Date(new Date().format('yyyy-MM-dd')).getTime();
                    }
                },
            }
        },
        computed: {
            //根据类型返回客户model
            formModel() {
                return this.orderData.cust.enterprise;
            },
            //生成中征码的校验，大项目时中征码必填，零售时非必填
            eigenCodeRule() {
                var rule = [];
                let validator = (rule, value, callback) => {
                    let filed = '中征码';
                    if (value === '' || value === null || value === undefined) {
                        if (this.formModel.orderApplicationType != '1')
                            callback(new Error(filed + '不能为空'));//2为大项目，必填
                        else
                            callback();//1为零售，非必填
                    } else if (String(value).length == 16 && !/\s/g.test(value)) {//要加上中征码的正则，16位
                        callback();
                    } else {
                        callback(new Error(filed + '格式不正确'));
                    }
                };
                rule.push({
                    validator: validator,
                    trigger: 'change'
                })
                return rule
            },
            //生成证书到期日期的校验
            certificateExpiryDateRule() {
                var rule = [];
                let validator = (rule, value, callback) => {
                    let filed = '证书到期日期';
                    if (value == '') {
                        if (this.formModel.isLongCertificate != '1')
                            callback(new Error(filed + '不能为空'));//非长期时必填
                        else
                            callback();//长期时不校验
                    } else if (Object.prototype.toString.call(value) == '[object Date]' || typeof value == 'string') {
                        callback();
                    } else {
                        callback(new Error(filed + '格式不对'));
                    }
                };
                rule.push({
                    validator: validator,
                    trigger: 'change'
                })
                return rule
            }
        },
        created() {
        },
        mounted() {
            this.queryDictData(['country', 'currency', 'applyType', 'whether', 'firmScale', 'orgStatus', 'economicType', 'industryLageClass', 'frCertType']);
            this.updateSmallClassDict();//初始化小类下拉
            this.updateOrgTypeDict();//初始化组织机构类别
            this.$nextTick(() => {
                this.todoRecount();
            })
        },
        methods: {
            //获取国别描述
            getCountryText(value) {
                var obj = this.dp['country'] ? this.dp['country'].find(item => item.dictCode == value) : '';
                return obj ? obj.dictName : '';
            },
            //选择国别
            selectCountry(item) {
                var change = this.formModel.country != item.dictCode;
                this.formModel.country = item.dictCode;
                this.showCountryDrop = false;
                if (change) this.handleChange(item.dictCode, 'enterprise.country');
            },
            //隐藏国别下拉
            hideCountryDrop() {
                this.showCountryDrop = false;
            },
            //获取币种描述
            getCurrencyText(value) {
                var obj = this.dp['currency'] ? this.dp['currency'].find(item => item.dictCode == value) : '';
                return obj ? obj.dictName : '';
            },
            //选择币种
            selectCurrency(item) {
                var change = this.formModel.registeredCapitalCurrency != item.dictCode;
                this.formModel.registeredCapitalCurrency = item.dictCode;
                this.showCurrencyDrop = false;
                if (change) this.handleChange(item.dictCode, 'enterprise.registeredCapitalCurrency');
            },
            //隐藏币种
            hideCurrencyDrop() {
                this.showCurrencyDrop = false;
            },
            //法人证件号码验证规则
            createCertNoRule() {
                var rule = [required('法人证件号码')];
                if (this.formModel.corporationCertificateType == '1' || this.formModel.corporationCertificateType == '7') {
                    rule.push({
                        validator: validators.certNo,
                        trigger: 'change'
                    })
                }
                return rule;
            },
            //接收识别结果方法
            setOcrRes(ocrRes) {
                if (ocrRes.flag == '2') {//营业执照
                    let hasChange = this.formModel.enterpriseName != ocrRes.enterpriseName ||
                        this.formModel.enterpriseBusinessScope != ocrRes.businessScope ||
                        this.formModel.unifiedSocialCreditCode != ocrRes.cfuscc ||
                        this.formModel.enterpriseRegistrationAddress != ocrRes.residenceAddress ||
                        (typeof ocrRes.registeredCapital == 'number' && this.formModel.registeredCapital != ocrRes.registeredCapital);
                    if (!hasChange) {
                        this.openEye(ocrRes.enterpriseName)
                        return false;
                    }
                    this.formModel.enterpriseName = ocrRes.enterpriseName;//企业名称
                    this.formModel.enterpriseBusinessScope = ocrRes.businessScope;//经营范围
                    this.formModel.unifiedSocialCreditCode = ocrRes.cfuscc;//统一社会信用代码
                    this.formModel.enterpriseRegistrationAddress = ocrRes.residenceAddress;//注册地
                    if (typeof ocrRes.registeredCapital == 'number') {
                        this.formModel.registeredCapital = ocrRes.registeredCapital//注册资本
                        this.$nextTick(() => {
                            this.$refs['formModel'].validateField('registeredCapital');
                        })
                    }
                    if (ocrRes.enterpriseName) this.openEye(ocrRes.enterpriseName)
                } else if (ocrRes.flag == '1') {//身份证
                    let hasChange = this.formModel.corporationCertificateNo != ocrRes.idcard ||
                        this.formModel.corporationName != ocrRes.name ||
                        this.formModel.corporationCertificateType != '1';
                    if (!hasChange) return false;
                    this.formModel.corporationName = ocrRes.name;//法人姓名
                    this.formModel.corporationCertificateNo = ocrRes.idcard;//身份证号
                    this.formModel.corporationCertificateType = '1';//身份证类型
                }
                this.$nextTick(() => {
                    this.cacheData('enterprise');
                    this.todoRecount()
                })
            },
            //天眼查
            openEye(enterpriseName) {//喜临门家具股份有限公司 '上海易鑫融资租赁有限公司';
                if (!enterpriseName) return false;
                this.showLoading();
                this.$http.get(polarisserver + '/customer/openEye' + '?enterpriseName=' + enterpriseName, {}).then((res) => {
                    this.hideLoading();
                    let result = res.data;
                    if (result.success && !!result.data) {
                        let _data = result.data;
                        let eyeRes = {
                            unifiedSocialCreditCode: _data.unifiedSocialCreditCode ? _data.unifiedSocialCreditCode : this.formModel.unifiedSocialCreditCode,//统一社会信用代码
                            enterpriseRegistrationAddress: _data.enterpriseRegistrationAddress ? _data.enterpriseRegistrationAddress : this.formModel.enterpriseRegistrationAddress,//注册地
                            organizationCode: _data.organizationCode ? _data.organizationCode : this.formModel.organizationCode,//组织机构代码
                            taxpayerIdentificationNum: _data.taxpayerIdentificationNum ? _data.taxpayerIdentificationNum : this.formModel.taxpayerIdentificationNum,//纳税人识别号
                            establishDate: _data.establishDate ? _data.establishDate : this.formModel.establishDate,//成立日期
                            registeredCapitalCurrency: _data.registeredCapitalCurrency ? _data.registeredCapitalCurrency : this.formModel.registeredCapitalCurrency,//注册资本币种
                            registeredCapital: typeof _data.registeredCapital == 'number' ? _data.registeredCapital : this.formModel.registeredCapital,//注册资本
                            enterpriseBusinessScope: _data.enterpriseBusinessScope ? _data.enterpriseBusinessScope : this.formModel.enterpriseBusinessScope,//经营范围
                            corporationName: _data.corporationName ? _data.corporationName : this.formModel.corporationName,//法人姓名
                            corporationIdentityCard: _data.corporationIdentityCard ? _data.corporationIdentityCard : this.formModel.corporationIdentityCard,//法人身份证号
                        }
                        let hasChange = this.formModel.unifiedSocialCreditCode != eyeRes.unifiedSocialCreditCode ||
                            this.formModel.enterpriseRegistrationAddress != eyeRes.enterpriseRegistrationAddress ||
                            this.formModel.organizationCode != eyeRes.organizationCode ||
                            this.formModel.taxpayerIdentificationNum != eyeRes.taxpayerIdentificationNum ||
                            this.formModel.establishDate != eyeRes.establishDate ||
                            this.formModel.registeredCapitalCurrency != eyeRes.registeredCapitalCurrency ||
                            this.formModel.registeredCapital != eyeRes.registeredCapital ||
                            this.formModel.enterpriseBusinessScope != eyeRes.enterpriseBusinessScope ||
                            this.formModel.corporationName != eyeRes.corporationName ||
                            this.formModel.corporationIdentityCard != eyeRes.corporationIdentityCard;
                        if (!hasChange) return;

                        this.formModel.unifiedSocialCreditCode = eyeRes.unifiedSocialCreditCode;//统一社会信用代码
                        this.formModel.enterpriseRegistrationAddress = eyeRes.enterpriseRegistrationAddress;//住所
                        this.formModel.organizationCode = eyeRes.organizationCode;//组织机构代码
                        this.formModel.taxpayerIdentificationNum = eyeRes.taxpayerIdentificationNum;//纳税人识别号
                        this.formModel.establishDate = eyeRes.establishDate;//成立日期
                        this.formModel.registeredCapitalCurrency = eyeRes.registeredCapitalCurrency;//注册资本币种
                        if (typeof _data.registeredCapital == 'number') {
                            this.formModel.registeredCapital = _data.registeredCapital;//注册资本
                            this.$nextTick(() => {
                                this.$refs['formModel'].validateField('registeredCapital');
                            })
                        }
                        this.formModel.corporationName = eyeRes.corporationName;//法人姓名
                        this.formModel.corporationCertificateNo = eyeRes.corporationCertificateNo;//身份证号
                        this.formModel.enterpriseBusinessScope = eyeRes.enterpriseBusinessScope;//经营范围
                        this.$nextTick(() => {
                            this.cacheData('enterprise');
                            this.todoRecount()
                        })
                    } else if (result.success) {
                        return this.$Message.error('天眼查请求无数据');
                    } else if (!result.success) {
                        return this.$Message.error('天眼查' + result.errorMessage || '请求失败');
                    }
                }).catch((err) => {
                    this.hideLoading();
                    this.errorCatchModal('天眼查请求出错：', err);
                })
            },
            //订单申请类型为零售时，中征码非必填
            todoorderApplicationTypeChange() {
                this.formModel.eigenCode = '';
                this.$refs['formModel'].validateField('eigenCode');
            },
            //长期时，到期日期非必填
            todoisLongCertificateChange() {
                this.formModel.certificateExpiryDate = '';
                this.$refs['formModel'].validateField('certificateExpiryDate');
            },
            //输入框失去焦点
            handleBlur(e) {
                let handleField = e.target.parentNode.getAttribute('v') || e.target.parentNode.parentNode.getAttribute('v');
                //判断值是否改变
                if (!this.checkFieldIsChange(handleField)) return false;
                //企业名称blur时调用天眼查
                if (handleField == 'enterprise.enterpriseName' && !!this.formModel.enterpriseName) {
                    this.openEye(this.formModel.enterpriseName);
                }
                this.$nextTick(() => {
                    this.cacheData('enterprise');
                    this.todoRecount();
                })
            },
            //下拉框改变值
            handleChange(val, handleField, regionChange) {
                //判断值是否改变
                if (!this.checkFieldIsChange(handleField) && !regionChange) return false;
                //经济行业大类改变时查询小类列表
                if (handleField == 'enterprise.economicIndustryLargeClass') {
                    this.updateSmallClassDict();
                }
                if (handleField == "enterprise.orderApplicationType") {//订单申请类型为大项目时中征码必填
                    this.todoorderApplicationTypeChange();
                }
                if (handleField == "enterprise.isLongCertificate") {//证书为长期时 证书为长期时到期日期改为非必填，并清空禁用
                    this.todoisLongCertificateChange();
                }
                if (handleField == 'enterprise.economicIndustryLargeClass') {
                    this.updateSmallClassDict();
                }

                this.$nextTick(() => {
                    if (handleField == "enterprise.corporationCertificateType" && this.formModel.corporationCertificateNo) {//证件类型切换时，校验
                        this.$refs['formModel'].validateField('corporationCertificateNo');
                    }
                    this.cacheData('enterprise')
                    this.todoRecount();
                })
            },
            //地区改变选择时，store赋值给有效字段,handleField是处理change时字段
            changeRegion(field, handleField) {
                this.setChangeRegion({
                    module: 'enterprise',
                    field: field,
                })
                this.handleChange(field, handleField, true)
            },
            //修改组织机构类别细分的字典   数据库暂时没有级联关系，未用到该方法
            updateOrgTypeDict(cb) {
                this.showLoading();
                this.$http.get(baseserver + '/dictionary/getOrganizationCategory', {}).then((res) => {
                    this.hideLoading();
                    let result = res.data;
                    if (!result.success) return this.errorModal(result.errorMessage || '组织机构类别请求失败')
                    this.orgList = result.data || [];
                    this.DICT_orgType = this.orgList.filter(item => item.parent == 0);
                    cb && cb();
                }).catch((err) => {
                    this.hideLoading();
                    this.errorCatchModal('组织机构类别请求出错：', err);
                })
            },
            //修改小类的字典
            updateSmallClassDict() {
                let pid = this.formModel.economicIndustryLargeClass;
                if (!pid) {
                    this.$set(this.dp, 'industrySmallClass', [])
                    return;
                }
                this.$http.get(baseserver + '/dictionary/getNextDictionaryList' + '/AL:' + pid, {}).then((res) => {
                    let result = res.data;
                    if (!result.success) return this.errorModal(result.errorMessage || '请求经济行业小类失败')
                    this.$set(this.dp, 'industrySmallClass', result.data)
                }).catch((err) => {
                    this.errorCatchModal('请求经济行业小类出错：', err);
                })
            },
            //校验当前担保人，可由父页面调用
            validate(cb) {
                let refName = ['formModel', 'regionRef1', 'regionRef2'];
                let arr = [];
                refName.forEach(ref => {
                    arr.push(new Promise((resolve) => {
                        if (this.$refs[ref]) this.$refs[ref].validate((valid) => {
                            resolve(valid);
                        });
                        else resolve(true)
                    }))
                })
                Promise.all(arr).then((result) => {
                    var flag = _.every(result);
                    cb && cb(flag);
                });
            },
        },
    }
</script>



