<template>
    <div class="apply-client-module">
        <!--个人申请人表单-->
        <order-personal-form ref="personalForm" class="cm-panel" v-if="personal && !isQy"></order-personal-form>
        <!--企业申请人表单-->
        <order-enterprise-form ref="enterpriseForm" :tokenInited="tokenInited" class="cm-panel"
                               v-if="enterprise && isQy">
        </order-enterprise-form>

        <!--个人申请人的亲属表单-->
        <order-relative-form ref="relativeForm" class="cm-panel" v-if="relative && !isQy"></order-relative-form>

        <!--联系人表单-->
        <order-contactor-form ref="contactorForm" v-if="contactor && contactor.length"></order-contactor-form>

        <!--添加担保人表单-->
        <div class="cm-panel" v-if="guarantorPersonal.length==0 && guarantorEnterprise.length==0">
            <h3 class="margin-b-14">担保人 <span v-if="!isQy">(选填)</span></h3>
            <div class="font0" style="margin-bottom:-10px;">
                <Button @click="addBondsman('1')" type="primary" ghost custom-icon="iconfont icon-add">添加个人担保人</Button>
                <Button @click="addBondsman('2')" type="primary" ghost custom-icon="iconfont icon-add">添加企业担保人</Button>
            </div>
        </div>
        <!--个人担保人列表-->
        <div class="cm-panel" v-for="(guarantorPersonalModel,i) in guarantorPersonal">
            <order-guarantor-personal-form :ref="'guarantorPersonal'+i" type="1" :index="i"
                                           @on-delete="deleteBondsman('1',i)"></order-guarantor-personal-form>
        </div>
        <!--企业担保人列表-->
        <div class="cm-panel" v-for="(guarantorEnterpriseModel,i) in guarantorEnterprise">
            <order-guarantor-enterprise-form :ref="'guarantorEnterprise'+i" :index="i"
                                             @on-delete="deleteBondsman('2',i)"></order-guarantor-enterprise-form>
        </div>
    </div>
</template>

<script>
    /**
     * @Description: 客户信息标签页
     */
    import orderPersonalForm from "./personal.vue";
    import orderEnterpriseForm from "./enterprise.vue";
    import orderRelativeForm from "./relative.vue";
    import orderContactorForm from "./contactor.vue";
    import orderGuarantorPersonalForm from "./g_personal.vue";
    import orderGuarantorEnterpriseForm from "./g_enterprise.vue";

    import base from "./js/custbase";
    import TRANS from "./js/trans";

    import {yx, scrollTo} from "@/utils/helper";
    import COSFN from "@/utils/cos";

    export default {
        name: 'orderCustModule',
        mixins: [base],
        components: {
            orderPersonalForm,
            orderEnterpriseForm,
            orderContactorForm,
            orderGuarantorPersonalForm,
            orderGuarantorEnterpriseForm,
            orderRelativeForm,
        },
        props: {},
        data() {
            return {
                inited: true,
                hasError: false,
                tokenInited: false,
            }
        },
        destroy() {
        },
        computed: {
            orderinfo() {
                return this.orderData.orderinfo || {};
            },
        },
        mounted() {
            this.queryDictData(['province']).then(dp => {
                if (!dp.province || !dp.province.length) {
                    this.errorModal('请求省份数据为空');
                }
            });
            //初始化请求客户信息
            this.queryInit();
            //初始实例化COS，整个编辑页共用一个
            var params = {
                "orderId": this.orderNo,
                "viewCode": "yixin_xiaofei_tibao_polaris",
                "orderUserId": this.account,
                "orderUserName": this.userName
            }
            this.COS = new COSFN(params, token => {
                this.SET_ORDER_COS(this.COS);//设置订单COS
                this.tokenInited = true;
            })
        },
        destroyed() {
            //离开时将订单的COS置空
            this.SET_ORDER_COS(undefined);
            sessionStorage.removeItem('cust'); //页面销毁时清除cust缓存
        },
        methods: {
            //加载客户所有信息
            async queryInit() {
                if (!this.isQy) {
                    //个人申请人信息
                    let custInfo = await this.queryInitData('personal');
                    //亲属信息
                    //let relativeInfo = await this.queryInitData('relative');
                } else if (this.isQy) {
                    let a = await this.queryInitData('enterprise');
                } else {
                    return false
                }
                //联系人
                let contactor = await this.queryInitData('contactor');
                //所有担保人信息
                let guarantors = await this.queryInitData('guarantor');
                this.$nextTick(() => {
                    this.todoRecount();
                })
            },
            //新增担保人
            addBondsman(type) {
                if (type == '1')
                    this.addOrderGuarantorPersonal();
                else if (type == '2')
                    this.addOrderGuarantorEnterprise();
            },
            //删除当前担保人
            deleteBondsman(type, index) {
                this.$Modal.confirm({
                    title: '提示',
                    content: '您确定要删除担保人信息吗？',
                    onOk: () => {
                        if (type == '1') {
                            this.guarantorPersonal.splice(index, 1);
                            this.cacheData('guarantorPersonal')
                            this.todoRecount()
                        } else if (type == '2') {
                            this.guarantorEnterprise.splice(index, 1);
                            this.cacheData('guarantorEnterprise')
                            this.todoRecount()
                        }
                    }
                })
            },

            //转换保存需要的数据并返回
            getSaveData() {
                let orderinfo = this.orderData.orderinfo;
                let url;
                let data = {
                    contactInfoList: TRANS.transToSave('contactor', this.orderData.cust),
                    personalGuarantor: TRANS.transToSave('guarantorPersonal', this.orderData.cust),
                    enterpriseGuarantor: TRANS.transToSave('guarantorEnterprise', this.orderData.cust)
                }

                data.contactInfoList.map((item) => {
                    item.orderNo = orderinfo.orderNo;
                    delete item.id;//删除属性
                });
                if (data.personalGuarantor.length) {
                    data.personalGuarantor = data.personalGuarantor[0];
                    data.personalGuarantor.orderNo = orderinfo.orderNo;
                    delete data.personalGuarantor.id;//删除属性
                } else {
                    data.personalGuarantor = undefined;
                }
                if (data.enterpriseGuarantor.length) {
                    data.enterpriseGuarantor = data.enterpriseGuarantor[0];
                    data.enterpriseGuarantor.orderNo = orderinfo.orderNo;

                    //data.enterpriseGuarantor.enterpriseContactPhone = data.enterpriseGuarantor.corporationPhone;
                    delete data.enterpriseGuarantor.id;//删除属性
                } else {
                    data.enterpriseGuarantor = undefined;
                }
                if (this.isQy) {//企业
                    let newResult = TRANS.transToSave('enterprise', this.orderData.cust);
                    data.enterprise = Object.assign({}, newResult['enterpriseApplier'], newResult['enterpriseLicense']);
                    data.enterprise.orderNo = orderinfo.orderNo;
                    //   data.enterprise.registrationType = '12';
                    delete data.enterprise.id;//删除属性
                } else {//个人
                    data.personal = TRANS.transToSave('personal', this.orderData.cust);
                    data.personal.orderNo = orderinfo.orderNo;
                    data.relationship = TRANS.transToSave('relative', this.orderData.cust);
                    data.relationship.orderNo = orderinfo.orderNo;
                    delete data.personal.id;//删除属性
                    delete data.relationship.id;//删除属性
                }
                return data;
            },
            //保存全部客户信息
            toSave(submitFlag) {
                return new Promise((resolve, reject) => {
                    this.validate(valid => {
                        if (!valid) {
                            resolve(false)
                        } else {
                            let saveData = this.getSaveData();
                            saveData.saveFrom = 'PC';
                            this.showLoading();
                            let url = "";
                            if (this.isQy) {//企业
                                url = polarisserver + '/customer/enterprise/submit';
                            } else {
                                url = polarisserver + '/customer/personal/submit';
                            }
                            this.$http.post(url, saveData).then((res) => {
                                this.hideLoading();
                                let result = res.data;
                                if (!result.success) {
                                    this.setOrderCustTabSaved(false)
                                    this.errorModal(result.errorMessage || '保存失败');
                                    resolve(false)
                                } else {
                                    this.setOrderCustTabSaved(true);
                                    //提交标识，提交前调用的话不用提示
                                    if (!submitFlag) this.$Message.success('客戶信息保存成功!')
                                    resolve(true)
                                }
                            }).catch((err) => {
                                this.hideLoading();
                                this.setOrderCustTabSaved(false)
                                this.errorCatchModal('客戶信息保存请求出错：', err);
                                reject(err)
                            })
                        }
                    })
                })
            },
            scrollToValid(flag) {
                this.$nextTick(() => {
                    if (!flag) {
                        let el = $('.apply-client-module .ivu-form-item-error')[0];
                        if (el && el.getBoundingClientRect) {
                            let posObj = el.getBoundingClientRect();//top/right/bottom/left
                            let st = document.documentElement.scrollTop || document.body.scrollTop;
                            let total = posObj.top + st - 200;
                            //滚定到位置
                            scrollTo(document, total, 300);
                        }
                    }
                    this.setOrderModuleError({index: 0, hasError: !flag})
                })
            },
            //客户信息全部校验，回传给父级页
            validate(cb) {
                let arr = [], refName = [];
                let baseName = this.isQy ? 'enterpriseForm' : 'personalForm';
                refName.push(baseName); //客户基本信息
                if (!this.isQy) refName.push('relativeForm'); //个人客户-亲属
                refName.push('contactorForm');//联系人表单
                if (this.guarantorPersonal && this.guarantorPersonal.length) refName.push('guarantorPersonal0'); //个人担保人
                if (this.guarantorEnterprise && this.guarantorEnterprise.length) refName.push('guarantorEnterprise0'); //企业担保人
                refName.forEach(ref => {
                    var vDom = this.$refs[ref];
                    //数组遍历出来的需要加[0]
                    if (ref == 'guarantorPersonal0' || ref == 'guarantorEnterprise0')
                        vDom = vDom[0];
                    if (vDom) arr.push(new Promise((resolve) => {
                        if (vDom) vDom.validate((valid) => {
                            resolve(valid);
                        });
                        else resolve(true)
                    }))
                })
                Promise.all(arr).then((result) => {
                    var flag = _.every(result);
                    this.scrollToValid(flag);
                    cb && cb(flag);
                });
            }
        },
    }
</script>



