<template>
    <div>
        <Form ref="formModel" :model="formModel" :rules="guarantorEnterpriseRule" :label-width="120" v-if="formModel">
            <div>
                <h3 class="margin-b-14">企业担保人
                    <ocr-button itemid="guarantorBusinessLicence" ocrtype="2" @on-ocr-result="setOcrRes">上传营业执照部分免填写
                    </ocr-button>
                    <ocr-button itemid="guarantorIDPositive" ocrtype="1" @on-ocr-result="setOcrRes">
                        上传担保人身份证部分免填写
                    </ocr-button>
                    <Button type="text" size="small" style="float: right;"
                            @click="deleteBondsman">删除
                    </Button>
                </h3>
                <Row>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="与申请人关系" prop="guarantorRelationship">
                            <i-select type="text" v-model.sync="formModel.guarantorRelationship" placeholder="请选择"
                                      @on-change="val=>{ handleChange(val,'guarantorEnterprise.guarantorRelationship')}">
                                <i-option v-for="(item,index) in dp['relationship']" :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="企业名称" prop="enterpriseName">
                            <Input clearable v-model.sync="formModel.enterpriseName" placeholder="请输入"
                                   v="guarantorEnterprise.enterpriseName"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="企业性质" prop="enterpriseNature">
                            <i-select v-model.sync="formModel.enterpriseNature" placeholder="请选择"
                                      @on-change="val=>{ handleChange(val,'guarantorEnterprise.enterpriseNature')}">
                                <i-option v-for="(item,index) in dp['firmNature']" :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </FormItem>
                    </i-col>

                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="统一社会信用代码" prop="unifiedSocialCreditCode">
                            <Input clearable v-model.sync="formModel.unifiedSocialCreditCode" placeholder="请输入"
                                   v="guarantorEnterprise.unifiedSocialCreditCode"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="法人姓名" prop="corporationName">
                            <Input clearable v-model.sync="formModel.corporationName" placeholder="请输入"
                                   v="guarantorEnterprise.corporationName"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="法人证件号码" prop="corporationIdentityCard">
                            <Input clearable v-model.sync="formModel.corporationIdentityCard" placeholder="请输入"
                                   v="guarantorEnterprise.corporationIdentityCard"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="法人联系电话" prop="corporationPhone">
                            <Input clearable v-model.sync="formModel.corporationPhone" placeholder="请输入"
                                   v="guarantorEnterprise.corporationPhone"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="实际控制人姓名" prop="actualControllerName">
                            <Input clearable v-model.sync="formModel.actualControllerName" placeholder="请输入"
                                   v="guarantorEnterprise.actualControllerName"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="实际控制人证件号码" prop="actualControllerIdentityCard">
                            <Input clearable v-model.sync="formModel.actualControllerIdentityCard" placeholder="请输入"
                                   v="guarantorEnterprise.actualControllerIdentityCard"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="实际控制人联系电话" prop="actualControllerPhone">
                            <Input clearable v-model.sync="formModel.actualControllerPhone" placeholder="请输入"
                                   v="guarantorEnterprise.actualControllerPhone"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="企业注册地" class="hascol-form-item">
                            <region-picker type="3" v-model="formModel.enterpriseRegistration_Region"
                                           regionlabel="企业注册地" ref="regionRef1"
                                           @on-change="changeRegion('enterpriseRegistration','guarantorEnterprise.enterpriseRegistration_Region')"></region-picker>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="企业注册地详细地址" prop="enterpriseRegistrationAddress">
                            <Input clearable v-model.sync="formModel.enterpriseRegistrationAddress" placeholder="请输入"
                                   v="guarantorEnterprise.enterpriseRegistrationAddress"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="企业经营地" class="hascol-form-item">
                            <region-picker type="3" v-model="formModel.enterpriseManagement_Region"
                                           regionlabel="企业经营地" ref="regionRef2"
                                           @on-change="changeRegion('enterpriseManagement','guarantorEnterprise.enterpriseManagement_Region')"></region-picker>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="企业经营地详细地址" prop="enterpriseManagementAddress">
                            <Input clearable v-model.sync="formModel.enterpriseManagementAddress" placeholder="请输入"
                                   v="guarantorEnterprise.enterpriseManagementAddress"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                </Row>
            </div>
        </Form>
    </div>
</template>
<script>
    /**
     * @Description: 企业担保人表单
     * @Props: {index: Number} 当前担保人在进件数据中的索引值
     */
    import base from './js/custbase';
    import rules from './js/custRules';

    export default {
        name: 'orderGuarantorEnterpriseForm',
        mixins: [base],
        props: {
            index: {
                default: 0
            },
        },
        components: {},
        data() {
            return {
                guarantorEnterpriseRule: rules.guarantorEnterpriseRule, //企业担保人校验规则
                targetObj: {},
            }
        },
        computed: {
            //根据类型返回担保人model
            formModel() {
                let obj = this.orderData.cust.guarantorEnterprise[this.index];
                if (!obj) return undefined;
                return obj;
            },
        },
        mounted() {
            this.queryDictData(['relationship', 'firmNature']);
            this.$nextTick(() => {
                this.todoRecount();
            })
        },
        methods: {
            //由父页面删除数据
            deleteBondsman() {
                this.$emit('on-delete', '2', this.index); //可以不传参
            },
            handleBlur(e) {
                let handleField = e.target.parentNode.getAttribute('v') || e.target.parentNode.parentNode.getAttribute('v');
                let items = handleField.split('.');
                items.splice(1, 0, this.index);
                handleField = items.join('.')
                //判断值是否改变
                if (!this.checkFieldIsChange(handleField)) return false;
                if (handleField == 'guarantorEnterprise.0.enterpriseName') {
                    this.openEye(this.formModel.enterpriseName);
                }
                this.$nextTick(() => {
                    this.cacheData('guarantorEnterprise')
                    this.todoRecount();
                })
            },
            //下拉框改变值
            handleChange(val, handleField, regionChange) {
                let items = handleField.split('.');
                items.splice(1, 0, this.index);
                handleField = items.join('.')
                //判断值是否改变
                if (!this.checkFieldIsChange(handleField) && !regionChange) return false;
                this.$nextTick(() => {
                    this.cacheData('guarantorEnterprise')
                    this.todoRecount();
                })
            },
            //地区改变选择时，store赋值给有效字段
            changeRegion(field, handleField) {
                this.setChangeRegion({
                    module: 'guarantorEnterprise',
                    index: this.index,
                    field: field,
                })
                this.handleChange(field, handleField, true);
            },
            //校验当前担保人，可由父页面调用
            validate(cb) {
                let refName = ['formModel', 'regionRef1', 'regionRef2'];
                let arr = [];
                refName.forEach(ref => {
                    arr.push(new Promise((resolve) => {
                        if (this.$refs[ref]) this.$refs[ref].validate((valid) => {
                            resolve(valid);
                        });
                        else resolve(true)
                    }))
                })
                Promise.all(arr).then(function (result) {
                    var flag = _.every(result);
                    cb && cb(flag);
                });
            },
            //识别结果回填
            setOcrRes(ocrRes) {
                if (ocrRes.flag == '2') {//营业执照
                    let hasChange = this.formModel.enterpriseName != ocrRes.enterpriseName ||
                        this.formModel.unifiedSocialCreditCode != ocrRes.cfuscc ||
                        this.formModel.enterpriseRegistrationAddress != ocrRes.residenceAddress;
                    if (!hasChange) {
                        this.openEye(ocrRes.enterpriseName)
                        return false;
                    }
                    this.formModel.enterpriseName = ocrRes.enterpriseName;//企业名称
                    this.formModel.unifiedSocialCreditCode = ocrRes.cfuscc;//统一社会信用代码
                    this.formModel.enterpriseRegistrationAddress = ocrRes.residenceAddress;//注册地
                    if (ocrRes.enterpriseName) this.openEye(ocrRes.enterpriseName)
                } else {//身份证
                    let hasChange = this.formModel.corporationIdentityCard != ocrRes.idcard ||
                        this.formModel.corporationName != ocrRes.name;
                    if (!hasChange) return false;
                    this.formModel.corporationName = ocrRes.name;//法人姓名
                    this.formModel.corporationIdentityCard = ocrRes.idcard;//身份证号
                }
                this.$nextTick(() => {
                    this.cacheData('guarantorEnterprise')
                    this.todoRecount();
                })
            },
            //天眼查
            openEye(enterpriseName) {//喜临门家具股份有限公司
                if (!enterpriseName) return false;
                this.showLoading();
                this.$http.get(polarisserver + '/customer/openEye' + '?enterpriseName=' + enterpriseName, {}).then((res) => {
                    this.hideLoading();
                    let result = res.data;
                    if (result.success && !!result.data) {
                        let _data = result.data;
                        let eyeRes = {
                            enterpriseName: _data.enterpriseName ? _data.enterpriseName : this.formModel.enterpriseName,//企业名称
                            unifiedSocialCreditCode: _data.unifiedSocialCreditCode ? _data.unifiedSocialCreditCode : this.formModel.unifiedSocialCreditCode,//统一社会信用代码
                            enterpriseRegistrationAddress: _data.enterpriseRegistrationAddress ? _data.enterpriseRegistrationAddress : this.formModel.enterpriseRegistrationAddress,//注册地
                            corporationName: _data.corporationName ? _data.corporationName : this.formModel.corporationName,//法人姓名
                            corporationIdentityCard: _data.corporationIdentityCard ? _data.corporationIdentityCard : this.formModel.corporationIdentityCard,//法人身份证号
                        }
                        let hasChange = this.formModel.enterpriseName != eyeRes.enterpriseName ||
                            this.formModel.unifiedSocialCreditCode != eyeRes.unifiedSocialCreditCode ||
                            this.formModel.enterpriseRegistrationAddress != eyeRes.enterpriseRegistrationAddress ||
                            this.formModel.corporationName != eyeRes.corporationName ||
                            this.formModel.corporationIdentityCard != eyeRes.corporationIdentityCard;
                        if (!hasChange) return;
                        this.formModel.enterpriseName = eyeRes.enterpriseName;//企业名称
                        this.formModel.unifiedSocialCreditCode = eyeRes.unifiedSocialCreditCode;//统一社会信用代码
                        this.formModel.enterpriseRegistrationAddress = eyeRes.enterpriseRegistrationAddress;//注册地
                        this.formModel.corporationName = eyeRes.corporationName;//法人姓名
                        this.formModel.corporationIdentityCard = eyeRes.corporationIdentityCard;//法人身份证号
                        this.$nextTick(() => {
                            this.cacheData('guarantorEnterprise')
                            this.todoRecount();
                        })
                    } else if (result.success) {
                        return this.$Message.error('天眼查请求无数据');
                    } else if (!result.success) {
                        return this.$Message.error(result.errorMessage || '天眼查请求失败');
                    }
                }).catch((err) => {
                    this.hideLoading();
                    this.errorCatchModal('天眼查请求出错：', err);
                })
            },
        },
    }
</script>



