import {mapState, mapGetters, mapMutations, mapActions} from 'vuex'

import regionPicker from "@/components/apply/regionPicker.vue";
import ocrButton from "@/components/apply/ocrButton.vue";

import TRANS from "./trans";
import {getCustFieldNum} from './custFields';
import rules from './custRules';
import {yx, stringToNumber} from '@/utils/helper';

export default {
    directives: {},
    components: {
        regionPicker,
        ocrButton
    },
    data() {
        return {
            DICT_gcertType: [],//个人担保人只能身份证
            rules: rules,
            personal: undefined,//申请人信息
            relative: undefined,//关系人信息
            contactor: undefined,//关系人信息
            enterprise: undefined,//企业全部信息
            e_applier: undefined,//企业基本信息
            e_license: undefined,//企业执照信息
            guarantorPersonal: [],//担保人个人信息
            guarantorEnterprise: [],//担保人企业信息

            errors: [],
            orderNo: this.$route.params.orderNo,
            isQy: yx.isQy(this.$route.params.customerType),
            account: this.$store.state.app.userInfo.userAccount,
            userName: this.$store.state.app.userInfo.userName,
        }
    },
    computed: {
        ...mapState('apply', [
            'orderData',
            'orderTabs',
            'ORDER_COS'
        ]),
        ...mapState({
            'dp': state => state.dict.dict
        }),
    },
    mounted() {
        this.queryDictData(['certType']).then(dp => {
            this.DICT_gcertType = dp['certType'].filter(item => item.dictCode == '1');//个人担保人只能身份证
        })
    },
    methods: {
        ...mapMutations('apply', ['SET_ORDER_COS', 'setCustData', 'setOrderCustTabSaved', 'setChangeRegion', 'setOrderModuleNumber', 'setOrderModuleError']),
        ...mapMutations('app', ['showLoading', 'hideLoading']),
        ...mapActions('dict', ['queryDictData']),
        errorModal(content, onOk, okText) {
            var arr = content.split(';');
            var text = '';
            arr.forEach(item => {
                if (item) text += !!text ? ('；<br>' + item) : item;
            })
            let options = {
                title: '提示',
                okText: okText || '确 定',
                content: text,
            };
            if (onOk) options.onOk = onOk;
            this.$Modal.error(options);
        },
        //更新客户tab中的错误，数字，缓存单独触发，不一起做
        todoRecount() {
            this.updateClientFieldsNumber();
            //不加的话报错样式会不显示，延迟以后会显示
            this.$nextTick(() => {
                let el = $('.apply-client-module .ivu-form-item-error').length;
                this.hasError = el > 0;
                this.setOrderModuleError({index: 0, hasError: this.hasError});
            })
        },
        //更新客户信息的个数
        updateClientFieldsNumber() {
            let params = getCustFieldNum(this.orderData.cust, this.isQy);
            this.setOrderModuleNumber({
                index: 0,
                data: params
            })
        },
        //添加个人担保人空数据
        addOrderGuarantorPersonal() {
            this.guarantorPersonal.push(TRANS.transSingleGuarantorPersonalToForm());
        },
        //添加企业担保人空数据
        addOrderGuarantorEnterprise() {
            this.guarantorEnterprise.push(TRANS.transSingleGuarantorEnterpriseToForm());
        },
        //输入框失焦或下位change时根据field名称获取旧值和新
        getHandleFieldValue(obj, handleField) {
            if (typeof obj != 'object' || !handleField) return undefined;
            var arr = handleField.split('.');
            var o = obj;
            for (let i in arr) {
                if (typeof o == 'object') {
                    let k = arr[i]
                    if (_.isArray(o)) k = stringToNumber(k);
                    o = o[k]
                }
            }
            return o;
        },
        //对比新旧值
        checkFieldIsChange(handleField) {
            var cust = this.orderData.cust;
            var oldCust = JSON.parse(sessionStorage.getItem('cust') || "{}")
            var val = this.getHandleFieldValue(cust, handleField),
                oldVal = this.getHandleFieldValue(oldCust, handleField);
            val = (typeof val == 'object' && (val instanceof Date)) ? new Date(val).format('yyyy-MM-dd') : (typeof val == 'object') ? JSON.stringify(val) : '' + val;
            oldVal = (typeof oldVal == 'object' && (oldVal instanceof Date)) ? new Date(oldVal).format('yyyy-MM-dd') : (typeof oldVal == 'object') ? JSON.stringify(oldVal) : '' + oldVal;
            if (val === oldVal) {
                return false;
            }
            return true;
        },
        //缓存
        cacheData(bs) {
            this.setOrderCustTabSaved(false);//如果触发缓存代表数据有变化
            //缓存当前数据，用于字段值的比较
            var sessionCust = JSON.parse(JSON.stringify(this.orderData.cust || {}))
            if (bs == 'enterprise') {
                var enObj = TRANS.transToSave(bs, this.orderData.cust) || {};
                sessionCust[bs] = Object.assign({}, enObj.enterpriseApplier, enObj.enterpriseLicense);
            } else {
                sessionCust[bs] = TRANS.transToSave(bs, this.orderData.cust);
            }
            sessionStorage.setItem('cust', JSON.stringify(sessionCust));
            //已经保存过的小模块不用再缓存
            if (this.orderData.hasSaved.cust || this.orderData.hasSaved[bs]) return false;
            const cacheKey = {
                personal: ':personal:applier',
                relative: ':personal:immediate',
                contactor: ':customer:contact',
                enterprise: ':enterprise:applier',
                e_applier: ':enterprise:applier',
                e_license: ':enterprise:license',
                guarantorPersonal: ':guarantor:personal',
                guarantorEnterprise: ':guarantor:enterprise',
            }
            let key = this.orderNo + cacheKey[bs];
            //企业基本信息和企业执照信息要同时缓存
            if (bs == 'enterprise') {
                let multiparams = [];
                let fullrow = TRANS.transToSave('enterprise', this.orderData.cust);
                fullrow.enterpriseApplier.orderNo = this.orderNo;
                fullrow.enterpriseApplier.hasSaved = this.orderData.hasSaved.e_applier;
                fullrow.enterpriseLicense.orderNo = this.orderNo;
                fullrow.enterpriseLicense.hasSaved = this.orderData.hasSaved.e_license;
                multiparams.push({
                    key: this.orderNo + cacheKey['e_applier'],
                    value: JSON.stringify(fullrow.enterpriseApplier)
                })
                multiparams.push({
                    key: this.orderNo + cacheKey['e_license'],
                    value: JSON.stringify(fullrow.enterpriseLicense)
                })
                this.$http.post(polarisserver + '/cache/insertMultiCache', multiparams).then(res => {
                    let result = res.data;
                    if (result.hasErrors) return this.$Message.error(result.errorMessage || '缓存保存失败');
                }).catch(err => {
                    this.errorCatchModal('缓存请求出错：', err);
                })
            } else {
                let row = TRANS.transToSave(bs, this.orderData.cust);
                if (_.isArray(row)) {
                    row.forEach(item => {
                        item.orderNo = this.orderNo;
                    })
                } else if (!!row) row.orderNo = this.orderNo;
                if (bs == 'guarantorPersonal' || bs == 'guarantorEnterprise') {
                    row = row[0] || "";
                }
                let params = {
                    key: key,
                    value: row ? JSON.stringify(row) : ""
                }
                this.$http.post(polarisserver + '/cache/insertCache', params).then(res => {
                    let result = res.data;
                    if (result.hasErrors) return this.$Message.error(result.errorMessage || '缓存保存失败');
                }).catch(err => {
                    this.errorCatchModal('缓存请求出错：', err);
                })
            }
        },

        //初始化数据  //没用这里的查询，还是用的编辑主页中的store
        async queryInitData(bs, cb) {
            var orderNo = '/' + this.$route.params.orderNo;
            var urls = {
                'personal': '/customer/personal/query',
                'enterprise': '/customer/enterprise/findBasic',
                'e_applier': '/customer/enterprise/findBasic',
                'e_license': '/customer/enterprise/findLicense',
                'relative': '/customer/relationship/query',
                'contactor': '/customer/contact/query',
                'guarantor': '/customer/guarantor/query',
            };
            var url = polarisserver + urls[bs] + `${orderNo}`;
            this.showLoading();
            this.$http.get(url).then(res => {
                this.hideLoading();
                if (!res.data.success) return this.errorModal(res.data.errorMessage || '请求失败')
                var data;
                switch (bs) {
                    case 'personal':
                        data = res.data.data || {};
                        this.personal = TRANS.transToForm(bs, data);
                        break;
                    case 'relative':
                        data = res.data.data || {};
                        this.relative = TRANS.transToForm(bs, data);
                        break;
                    case 'enterprise':
                        data = res.data.data || {};
                        break;
                    case 'contactor':
                        data = res.data.data;
                        this.contactor = TRANS.transToForm(bs, data);
                        break;

                    case 'guarantor':
                    case 'guarantorPersonal':
                    case 'guarantorEnterprise':
                        let guarantors = res.data.data || {};
                        var pArr = !!guarantors.guarantorPersonal ? TRANS.transToForm('guarantorPersonal', [guarantors.guarantorPersonal]) : [];
                        var eArr = !!guarantors.guarantorEnterprise ? TRANS.transToForm('guarantorEnterprise', [guarantors.guarantorEnterprise]) : [];
                        this.guarantorPersonal = pArr;
                        this.guarantorEnterprise = eArr;
                        break;
                }
                if (bs == 'enterprise') {
                    this.showLoading();
                    //同时请求执照
                    this.$http.get(polarisserver + urls['e_license'] + `${orderNo}`).then(res2 => {
                        this.hideLoading();
                        if (!res2.data.success) return this.errorModal(res2.data.errorMessage || '请求失败')
                        let licenseInfo = res2.data.data || {};
                        this.enterprise = TRANS.transToForm(bs, {
                            basicInfo: data || {},
                            licenseInfo: licenseInfo || {},
                        });
                        this.orderData.hasSaved.e_applier = data.hasSaved == 1;
                        this.orderData.hasSaved.e_license = licenseInfo.hasSaved == 1;
                        this.setCustData({
                            name: bs,
                            data: this[bs]
                        })
                        cb && cb(data);
                    }).catch(err2 => {
                        this.hideLoading();
                        this.errorCatchModal('企业执照信息请求出错：', err2)
                    });

                } else if (bs == 'personal') {
                    this.setCustData({
                        name: 'personal',
                        data: this['personal']
                    })
                    //同时请求亲属
                    this.queryInitData('relative', cb);
                } else if (bs == 'guarantor') {
                    this.setCustData({
                        name: 'guarantorPersonal',
                        data: this['guarantorPersonal']
                    })
                    this.setCustData({
                        name: 'guarantorEnterprise',
                        data: this['guarantorEnterprise']
                    })
                } else {
                    this.setCustData({
                        name: bs,
                        data: this[bs]
                    })
                    cb && cb(this[bs]);
                }
            }).catch(err => {
                this.hideLoading();
                this.errorCatchModal('客户' + bs + '信息请求出错：', err)
            });
        }
    },

}



