<template>
    <span>
        <input ref="uploadInput" type="file" style="display:none;" name="file"
               @change="changeImage($event)"
               accept="image/jpeg,image/jpg,image/png">
        <Button type="primary" ghost @click="selectFile()" custom-icon="iconfont icon-upload">
           <slot></slot>
        </Button>
    </span>
</template>
<script>
    /**
     * @Description: 进件提报客户页中使用的上传按钮
     * @Props: {itemid: String} 附件的itemID
     * @Props: {ocrtype: String} 上传文件类型  1身份证 2营业执照
     */
    import {mapState, mapGetters, mapMutations, mapActions} from 'vuex'

    import COSFN from "@/utils/cos";
    import {yx} from "@/utils/helper";

    export default {
        name: "ocrButton",
        mixins: [],
        props: {
            'itemid': '',
            'ocrtype': {
                default: '1' //默认为身份证 1=身份证2=营业执照
            },
        },
        data() {
            return {
                inited: false,//初始化是否完成
                hasUpload: true,  //图片上传前后
                thumbnailUrl: '',  //上传后图片路径
                targetObj: {},//操作数据
                orderNo: this.$route.params.orderNo,
                account: this.$store.state.app.userInfo.userAccount,
                userName: this.$store.state.app.userInfo.userName,
                title: '',//itemID对应云盘的名称
                COS: undefined,
            }
        },
        computed: {
            ...mapState('apply', [
                'ORDER_COS'
            ]),
        },
        mounted() {
            if (!this.itemid || !this.ocrtype || !this.account || !this.orderNo || !COSFN) {
                this.$Message.error('必要参数不全，无法上传附件并识别');
                return false;
            }
        },
        methods: {
            ...mapMutations('app', ['showLoading', 'hideLoading']),
            errorModal(content, onOk, okText) {
                let options = {
                    title: '提示',
                    okText: okText || '确 定',
                    content: content,
                };
                if (onOk) options.onOk = onOk;
                this.$Modal.error(options);
            },
            //上传前先清空input值
            selectFile() {
                if (!this.ORDER_COS) {
                    return this.$Message.error('附件上传参数未获取成功，请稍候再试');
                }
                this.$refs['uploadInput'].value = ''; //先清空文件选择框的值
                this.$refs['uploadInput'].click(); //输入框点击
            },
            //文件输入框选择后事件
            changeImage(e) {
                var files = e.target.files;
                if (!files || !files.length) return;
                this.$set(this.targetObj, 'flag', this.ocrtype);
                var file = files[0];
                //身份证 限制是3M，营业执照 限制是1M，超过的话调用压缩方法
                // 该大小限制针对base64的字节数算法，base64是源文件大小的4/3，即用文件大小判断的话需要再转一道*0.75
                var maxSize = this.ocrType == '2' ? 1024 * 1024 * 0.75 : 1024 * 1024 * 3 * 0.75;
                yx.optim(file, maxSize, (realFile) => {
                    if (realFile.size > maxSize) {
                        return this.errorModal('文件大小超过允许上传的最大值，请重新选择！')
                    }
                    this.uploadFiles(realFile);
                });
            },
            //上传图片到腾讯云
            uploadFiles(files) {
                let indexOf = files.name.lastIndexOf('.');
                let ext = '';
                if (indexOf > 0) ext = files.name.substring(indexOf + 1).toLowerCase();
                let sha = this.ORDER_COS.sha256.create();
                const fileresult = files.name + new Date().getTime();//用于区别生成hash名称
                sha.update(fileresult);
                let hash = sha.hex();
                let hashName = hash + '.' + ext;
                this.showLoading();
                this.ORDER_COS.cos.sliceUploadFile({
                    Bucket: this.ORDER_COS.options.Bucket,
                    Region: this.ORDER_COS.options.Region,
                    Key: this.ORDER_COS.options.remotePath + hashName,
                    Body: files,
                }, (err, data) => {
                    console.log('err', err)
                    this.hideLoading();
                    if (err == null) {//上传ok
                        if (data && data.statusCode == 200) {
                            this.$set(this.targetObj, 'ext', ext);
                            //通知云盘保存
                            this.$set(this.targetObj, 'itemID', this.itemid)
                            this.$set(this.targetObj, 'title', this.ORDER_COS.items[this.itemid])
                            this.$set(this.targetObj, 'name', hashName)
                            this.$set(this.targetObj, 'remoteKey', this.ORDER_COS.options.remotePath + hashName)
                            this.ORDER_COS.saveItemToYun(this.targetObj, (saveres) => {
                                if (!saveres) {
                                    this.errorModal('文件同步云盘失败');
                                    return false;
                                }
                                let item = saveres.items[0];
                                if (item.files && item.files.length) {
                                    let imgSrc = item.files[0].previewUrl;
                                    this.$set(this.targetObj, 'qcloudUrl', imgSrc)
                                    if (!imgSrc) {//无图片
                                        this.$set(this.targetObj, 'error', true);
                                        this.errorModal('获取上传后图片地址失败');
                                    } else {//返回有图片
                                        if (this.ocrtype == '2') {//营业执照转成base64后再识别
                                            let reader = new FileReader();
                                            reader.readAsDataURL(files);
                                            reader.onload = (e) => {
                                                this.$set(this.targetObj, 'ocrUrl', reader.result)
                                                this.todoOcr();
                                            };
                                        } else {//身份证
                                            this.$set(this.targetObj, 'ocrUrl', imgSrc)
                                            this.todoOcr();
                                        }
                                    }
                                } else {
                                    this.targetObj.error = true;
                                    this.errorModal('获取上传后文件数据失败');
                                }
                            })
                        }
                    } else {
                        this.errorModal('上传文件到腾讯云出错：' + err.statusCode + ' '+ err.error.Message);
                    }
                });
            },
            //ocr扫描身份证
            todoOcr() {
                let targetObj = this.targetObj,
                    url,
                    params;
                if (this.ocrtype == '2') {//营业执照
                    url = polarisserver + '/riskControl/scan';
                    params = {
                        type: 8,
                        format: targetObj.ext,
                        image: this.targetObj.ocrUrl.split(',')[1],//base64地址
                        uid: 'Polaris',
                        orderid: this.orderNo
                    };
                } else {//身份证
                    url = polarisserver + '/credit/scanIdCard';
                    params = {
                        "sysId": "2", //系统标识。app：1 ； pc：2
                        "cardType": '0', //身份证正反面标识 0：人像面 1：国徽面
                        "idcardUrlStr": this.targetObj.ocrUrl //也就是targetObj.qcloudUrl, //上传图片的腾讯云地址
                    };
                }
                this.showLoading();
                this.$http.post(url, params).then((res) => {
                    this.hideLoading();
                    let result = res.data;
                    if (!result.success) return this.errorModal(result.errorMessage || '识别失败，请重新上传!');
                    const ocrRes = result.data; //获取验证结果
                    if (!ocrRes)
                        return this.errorModal('识别失败，请重新上传!');
                    //缺少身份证已校验征信授权
                    ocrRes.flag = this.ocrtype;
                    this.$emit('on-ocr-result', ocrRes);
                }).catch((err) => {
                    this.hideLoading();
                    this.errorCatchModal('图片识别请求出错：', err);
                })
            },
        }
    }
</script>



