<template>
    <div>
        <h3 class="margin-b-14">个人信息</h3>
        <Form ref="formModel" :model="formModel" :rules="personalRule" :label-width="120">
            <Row>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="申请人姓名" prop="custName">
                        <Input v-model="formModel.custName" name="custName" disabled placeholder="请输入"
                               v="personal.custName"></Input>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="证件类型" prop="custCertificateType">
                        <i-select v-model.sync="formModel.custCertificateType" name="custCertificateType"
                                  disabled
                                  placeholder="请选择"
                                  @on-change="val=>{ handleChange(val,'personal.custCertificateType')}">
                            <i-option v-for="(item,index) in dp['certType']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="证件号码" prop="custCertificateNo">
                        <Input v-model="formModel.custCertificateNo" name="custCertificateNo" disabled placeholder="请输入"
                               v="personal.custCertificateNo"></Input>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="手机号码" prop="custPhone">
                        <Input v-model="formModel.custPhone" name="custPhone" disabled placeholder="请输入"
                               v="personal.custPhone"></Input>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="性别" prop="custGender">
                        <i-select v-model.sync="formModel.custGender" name="custGender"
                                  placeholder="请选择" @on-change="val=>{ handleChange(val,'personal.custGender')}">
                            <i-option v-for="(item,index) in dp['sex']" :value="item.dictCode" :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="婚姻状况" prop="marriage">
                        <i-select v-model.sync="formModel.marriage" name="marriage"
                                  placeholder="请选择" @on-change="val=>{ handleChange(val,'personal.marriage')}">
                            <i-option v-for="(item,index) in dp['marriage']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="申请人学历" prop="custEducation">
                        <i-select v-model.sync="formModel.custEducation" name="custEducation"
                                  placeholder="请选择" @on-change="val=>{ handleChange(val,'personal.custEducation')}">
                            <i-option v-for="(item,index) in dp['education']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="申请人学位" prop="custDegree">
                        <i-select v-model.sync="formModel.custDegree" name="custDegree"
                                  placeholder="请选择" @on-change="val=>{ handleChange(val,'personal.custDegree')}">
                            <i-option v-for="(item,index) in dp['degree']" :value="item.dictCode" :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="有无房产" prop="hasEstate">
                        <i-select v-model.sync="formModel.hasEstate" name="hasEstate"
                                  placeholder="请选择" @on-change="val=>{ handleChange(val,'personal.hasEstate')}">
                            <i-option v-for="(item,index) in dp['hasEstate']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="居住状况" prop="livingStatus">
                        <i-select v-model.sync="formModel.livingStatus" name="livingStatus"
                                  placeholder="请选择" @on-change="val=>{ handleChange(val,'personal.livingStatus')}">
                            <i-option v-for="(item,index) in dp['livingStatus']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="所属行业" prop="industry">
                        <i-select v-model.sync="formModel.industry" name="industry"
                                  placeholder="请选择" @on-change="val=>{ handleChange(val,'personal.industry')}">
                            <i-option v-for="(item,index) in dp['industry']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="税后年薪（万元）" prop="annualSalaryAfterTax">
                        <InputNumber clearable v-model="formModel.annualSalaryAfterTax" name="annualSalaryAfterTax"
                                     placeholder="请输入"
                                     v="personal.annualSalaryAfterTax"
                                     @on-blur="handleBlur" @on-clear="handleBlur"></InputNumber>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="申请人职业" prop="custProfession">
                        <i-select v-model.sync="formModel.custProfession" name="custProfession"
                                  placeholder="请选择" @on-change="val=>{ handleChange(val,'personal.custProfession')}">
                            <i-option v-for="(item,index) in dp['profession']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="申请人职务" prop="custPosition">
                        <i-select v-model.sync="formModel.custPosition" name="custPosition"
                                  placeholder="请选择" @on-change="val=>{ handleChange(val,'personal.custPosition')}">
                            <i-option v-for="(item,index) in dp['position']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="申请人职称" prop="custTitle">
                        <i-select v-model.sync="formModel.custTitle" name="custTitle"
                                  placeholder="请选择" @on-change="val=>{ handleChange(val,'personal.custTitle')}">
                            <i-option v-for="(item,index) in dp['title']" :value="item.dictCode" :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="企业性质" prop="companyNature">
                        <i-select v-model.sync="formModel.companyNature" name="companyNature"
                                  placeholder="请选择" @on-change="val=>{ handleChange(val,'personal.companyNature')}">
                            <i-option v-for="(item,index) in dp['firmNature']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="现居住地年限" prop="currentResidenceYears">
                        <Input clearable v-model="formModel.currentResidenceYears" name="currentResidenceYears"
                               placeholder="请输入" v="personal.currentResidenceYears" @on-blur="handleBlur"></Input>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="现居住地邮编" prop="postalCode">
                        <Input clearable v-model="formModel.postalCode" name="postalCode" placeholder="请输入"
                               v="personal.postalCode"
                               @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="16">
                    <FormItem label="单位名称" prop="companyName">
                        <Input clearable v-model="formModel.companyName" name="companyName" placeholder="请输入"
                               v="personal.companyName"
                               @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="16">
                    <FormItem label="单位所在地" class="hascol-form-item">
                        <region-picker type="3" v-model="formModel.company_Region"
                                       regionlabel="单位所在地" ref="regionRef1"
                                       @on-change="changeRegion('company', 'personal.company_Region')"></region-picker>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="16">
                    <FormItem label="单位详细地址" prop="companyAddress">
                        <Input v-model="formModel.companyAddress" placeholder="请输入" clearable
                               v="personal.companyAddress"
                               @on-blur="handleBlur"></Input>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="16">
                    <FormItem label="现居地" class="hascol-form-item">
                        <region-picker type="3" v-model="formModel.currentResidence_Region"
                                       regionlabel="现居地" ref="regionRef2"
                                       @on-change="changeRegion('currentResidence', 'personal.currentResidence_Region')"></region-picker>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="16">
                    <FormItem label="现居地详细地址" prop="currentResidenceAddress">
                        <Input v-model.sync="formModel.currentResidenceAddress" placeholder="请输入" clearable
                               v="personal.currentResidenceAddress" @on-blur="handleBlur"></Input>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="16">
                    <FormItem label="户籍地" class="hascol-form-item">
                        <region-picker type="3" v-model="formModel.censusRegister_Region"
                                       regionlabel="户籍地" ref="regionRef3"
                                       @on-change="changeRegion('censusRegister', 'personal.censusRegister_Region')"></region-picker>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="16">
                    <FormItem label="户籍地详细地址" prop="censusRegisterAddress">
                        <Input v-model.sync="formModel.censusRegisterAddress" placeholder="请输入" clearable
                               v="personal.censusRegisterAddress" @on-blur="handleBlur"></Input>
                    </FormItem>
                </i-col>
            </Row>
        </Form>
    </div>
</template>
<script>
    /**
     * @Description: 个人表单
     */
    import base from './js/custbase';
    import rules from './js/custRules';

    export default {
        name: 'orderPersonalForm',
        mixins: [base],
        props: {},
        components: {},
        data() {
            return {
                personalRule: rules.personalRule,
            }
        },
        computed: {
            //根据类型返回个人model
            formModel() {
                return this.orderData.cust.personal;
            },
        },
        created() {
        },
        mounted() {
            this.queryDictData(['certType', 'sex', 'marriage', 'education', 'degree', 'hasEstate', 'livingStatus', 'industry', 'profession', 'position', 'title', 'firmNature']);
            this.$nextTick(() => {
                this.todoRecount();
            })
        },
        methods: {
            //校验当前表单，可由父页面调用
            validate(cb) {
                let flag = true;
                let refName = ['formModel', 'regionRef1', 'regionRef2', 'regionRef3'];
                let arr = [];
                refName.forEach(ref => {
                    arr.push(new Promise((resolve) => {
                        if (this.$refs[ref]) this.$refs[ref].validate((valid) => {
                            resolve(valid);
                        });
                        else resolve(true)
                    }))
                })
                Promise.all(arr).then(function (result) {
                    flag = _.every(result);
                    cb && cb(flag);
                });
            },

            //输入框失去焦点
            handleBlur(e) {
                let handleField = e.target.parentNode.getAttribute('v') || e.target.parentNode.parentNode.getAttribute('v');
                //判断值是否改变
                if (!this.checkFieldIsChange(handleField)) return false;
                this.$nextTick(() => {
                    this.cacheData('personal');
                    this.todoRecount();
                })
            },
            //下拉框改变值
            handleChange(val, handleField, regionChange) {
                //判断值是否改变
                if (!this.checkFieldIsChange(handleField) && !regionChange) return false;
                this.$nextTick(() => {
                    this.cacheData('personal');
                    this.todoRecount();
                })
            },
            //地区改变选择时，store赋值给有效字段
            changeRegion(field, handleField) {
                this.setChangeRegion({
                    module: 'personal',
                    field: field,
                })
                this.handleChange(field, handleField, true)
            }
        },
        watch: {}
    }
</script>



