<template>
    <div>
        <Form ref="formModel" :model="formModel" :rules="guarantorPersonalRule" :label-width="120" v-if="formModel">
            <div>
                <h3 class="margin-b-14">个人担保人
                    <ocr-button itemid="guarantorIDPositive" ocrtype="1" @on-ocr-result="setOcrRes">上传身份证部分免填写
                    </ocr-button>
                    <Button type="text" size="small" style="float: right;"
                            @click="deleteBondsman">删除
                    </Button>
                </h3>
                <Row>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="与申请人关系" prop="guarantorRelationship">
                            <i-select type="text" v-model.sync="formModel.guarantorRelationship" placeholder="请选择"
                                      @on-change="val=>{ handleChange(val,'guarantorPersonal.guarantorRelationship')}">
                                <i-option v-for="(item,index) in dp['relationship']" :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="担保人姓名" prop="guarantorName">
                            <Input type="text" clearable
                                   v-model.sync="formModel.guarantorName" placeholder="请输入"
                                   v="guarantorPersonal.guarantorName"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="手机号码" prop="guarantorPhone">
                            <Input clearable
                                   v-model.sync="formModel.guarantorPhone" placeholder="请输入"
                                   v="guarantorPersonal.guarantorPhone"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="证件类型" prop="guarantorCredentialType">
                            <i-select type="text" disabled
                                      v-model.sync="formModel.guarantorCredentialType" placeholder="请选择"
                                      @on-change="val=>{ handleChange(val,'guarantorPersonal.guarantorCredentialType')}">
                                <i-option v-for="(item,index) in DICT_gcertType"
                                          :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="证件号码" prop="guarantorIdentityCard">
                            <Input clearable
                                   v-model.sync="formModel.guarantorIdentityCard" placeholder="请输入"
                                   v="guarantorPersonal.guarantorIdentityCard"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="单位电话" prop="companyPhone">
                            <Input clearable
                                   v-model.sync="formModel.companyPhone" placeholder="请输入"
                                   v="guarantorPersonal.companyPhone"
                                   @on-blur="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="单位名称" prop="companyName">
                            <Input clearable
                                   v-model.sync="formModel.companyName" placeholder="请输入"
                                   v="guarantorPersonal.companyName"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="单位所在地" class="hascol-form-item">
                            <region-picker type="3" v-model="formModel.company_Region"
                                           regionlabel="单位所在地" ref="regionRef1"
                                           @on-change="changeRegion('company','guarantorPersonal.company_Region')"></region-picker>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="单位详细地址" prop="companyDetailAddress">
                            <Input clearable
                                   v-model.sync="formModel.companyDetailAddress" placeholder="请输入"
                                   v="guarantorPersonal.companyDetailAddress"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="现居地" class="hascol-form-item">
                            <region-picker type="3" v-model="formModel.currentResidence_Region"
                                           regionlabel="现居地" ref="regionRef2"
                                           @on-change="changeRegion('currentResidence','guarantorPersonal.currentResidence_Region')"></region-picker>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="现居地详细地址" prop="currentResidenceAddress">
                            <Input clearable
                                   v-model.sync="formModel.currentResidenceAddress" placeholder="请输入"
                                   v="guarantorPersonal.currentResidenceAddress"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="户籍地" class="hascol-form-item">
                            <region-picker type="3" v-model="formModel.censusRegister_Region"
                                           regionlabel="户籍地" ref="regionRef3"
                                           @on-change="changeRegion('censusRegister','guarantorPersonal.censusRegister_Region')"></region-picker>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="户籍地详细地址" prop="censusRegisterAddress">
                            <Input clearable
                                   v-model.sync="formModel.censusRegisterAddress" placeholder="请输入"
                                   v="guarantorPersonal.censusRegisterAddress"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                </Row>
            </div>
        </Form>
    </div>
</template>
<script>
    /**
     * @Description: 个人担保人表单
     * @Props: {index: Number} 当前担保人在进件数据中的索引值
     */
    import base from './js/custbase';
    import rules from './js/custRules';
    import {validators, required} from "@/utils/validateRules";

    export default {
        name: 'orderGuarantorPersonalForm',
        mixins: [base],
        props: {
            index: {
                default: 0
            },
        },
        components: {},
        data() {
            return {
                guarantorPersonalRule: rules.guarantorPersonalRule, //个人担保人校验规则
                targetObj: {},
            }
        },
        computed: {
            //根据类型返回担保人model
            formModel() {
                let obj = this.orderData.cust.guarantorPersonal[this.index];
                if (!obj) return undefined;
                return obj;
            },
        },
        mounted() {
            this.$nextTick(() => {
                this.todoRecount();
            })
        },
        methods: {
            //证件号码验证规则
            createCertNoRule() {
                var rule = [required('证件号码')];
                if (this.formModel.guarantorCredentialType == '1' || this.formModel.guarantorCredentialType == '7') {
                    rule.push({
                        validator: validators.certNo,
                        trigger: 'change'
                    })
                }
                return rule;
            },
            //由父页面删除数据
            deleteBondsman() {
                this.$emit('on-delete', '1', this.index); //可以不传参
            },
            //输入框失去焦点
            handleBlur(e) {
                let handleField = e.target.parentNode.getAttribute('v') || e.target.parentNode.parentNode.getAttribute('v');
                let items = handleField.split('.');
                items.splice(1, 0, this.index);
                handleField = items.join('.')
                //判断值是否改变
                if (!this.checkFieldIsChange(handleField)) return false;
                this.$nextTick(() => {
                    this.cacheData('guarantorPersonal');
                    this.todoRecount();
                })
            },

            //下拉框改变值
            handleChange(val, handleField, regionChange) {
                let items = handleField.split('.');
                items.splice(1, 0, this.index);
                handleField = items.join('.')
                //判断值是否改变
                if (!this.checkFieldIsChange(handleField) && !regionChange) return false;
                this.$nextTick(() => {
                    if (handleField == "guarantorPersonal.guarantorCredentialType" && this.formModel.guarantorIdentityCard) {//证件类型切换时，校验
                        this.$refs['formModel'].validateField('guarantorIdentityCard');
                    }
                    this.cacheData('guarantorPersonal');
                    this.todoRecount();
                })
            },
            //地区改变选择时，store赋值给有效字段
            changeRegion(field, handleField) {
                this.setChangeRegion({
                    module: 'guarantorPersonal',
                    index: this.index,
                    field: field,
                })
                this.handleChange(field, handleField, true);
            },
            //校验当前担保人，可由父页面调用
            validate(cb) {
                let flag = true;
                let refName = ['formModel', 'regionRef1', 'regionRef2', 'regionRef3'];
                let arr = [];
                refName.forEach(ref => {
                    arr.push(new Promise((resolve) => {
                        if (this.$refs[ref]) this.$refs[ref].validate((valid) => {
                            resolve(valid);
                        });
                        else resolve(true)
                    }))
                })
                Promise.all(arr).then(function (result) {
                    flag = _.every(result);
                    cb && cb(flag);
                });
            },
            //识别结果回填
            setOcrRes(ocrRes) {
                var hasChange = this.formModel.guarantorIdentityCard != ocrRes.idcard ||
                    this.formModel.guarantorName != ocrRes.name ||
                    this.formModel.censusRegisterAddress != ocrRes.address;
                if (!hasChange) return false;
                this.formModel.guarantorIdentityCard = ocrRes.idcard;//身份证号
                this.formModel.guarantorName = ocrRes.name;//姓名
                this.formModel.guarantorCredentialType = "1";//身份证类型
                this.formModel.censusRegisterAddress = ocrRes.address || '';//户籍地详细地址
                this.$nextTick(() => {
                    this.cacheData('guarantorPersonal');
                    this.todoRecount();
                })
            },
        },
        watch: {}
    }
</script>



