import {
    validators,
    required,
    pctValidator,
    amountValidator,
} from "@/utils/validateRules";

export default {
    //TODO :个人客户表单
    personalRule: {
        'custName': [validators.custName('申请人姓名')],
        'custGender': [required('申请人性别')],
        'custPhone': [required('申请人手机'), {
            validator: validators.mobile,
            trigger: 'change'
        }],
        'custCertificateType': [required('申请人证件类型')],
        'custCertificateNo': [required('申请人证件号码'), {
            validator: validators.certNo,
            trigger: 'change'
        }],
        'marriage': [required('婚姻状况')],
        'custEducation': [required('申请人学历')],
        'custDegree': [required('申请人学位')],
        'hasEstate': [required('有无房产')],
        'livingStatus': [required('居住情况')],
        'industry': [required('所属行业')],
        'companyNature': [required('企业性质')],
        'companyName': [validators.headAndTailNotHasBlank('单位名称', true), {
            max: 100,
            message: '单位名称超过100字符',
            trigger: 'change'
        }],
        'custProfession': [required('申请人职业')],
        'custPosition': [required('申请人职务')],
        'custTitle': [required('申请人职称')],
        'annualSalaryAfterTax': [amountValidator('税后年薪', true, 1000)],

        'currentResidenceYears': [validators.yearsValidator('现居住地年限', true)],
        'postalCode': [validators.zipValidator('现居住地邮编', true)],

        company_Region: [validators.regionValidator('单位所在', 3)],
        'companyProvince': [required('单位所在省份')],
        'companyCity': [required('单位所在城市')],
        'companyDistrict': [required('单位所在区/县')],
        'companyAddress': [validators.addressValidator('单位详细地址', true)],

        currentResidence_Region: [validators.regionValidator('现居地', 3)],
        'currentResidenceProvince': [required('现居地省份')],
        'currentResidenceCity': [required('现居地城市')],
        'currentResidenceDistrict': [required('现居住地区/县')],
        'currentResidenceAddress': [validators.addressValidator('现居地详细地址', true)],

        censusRegister_Region: [validators.regionValidator('户籍地', 3)],
        'censusRegisterProvince': [required('户籍地省份')],
        'censusRegisterCity': [required('户籍地城市')],
        'censusRegisterDistrict': [required('户籍地区/县')],
        'censusRegisterAddress': [validators.addressValidator('户籍地详细地址', true)],

    },
    //TODO : 企业客户表单
    enterpriseRule: {
        'orderApplicationType': [required('订单申请类型')],
        'unifiedSocialCreditCode': [required('统一社会信用代码'),
            {validator: validators.isCreditCode, trigger: 'change'}],
        'enterpriseName': [validators.headAndTailNotHasBlank('企业名称', true), {
            max: 100,
            message: '公司名称超过100字符',
            trigger: 'change'
        }],
        'country': [required('国别')],
        'isInstitution': [required('是否事业单位')],
        'registrationType': [required('登记注册号类型')],
        'eigenCode': [],
        'organizationCode': [required('组织机构代码'),
            {validator: validators.checkOrgCodeValid, trigger: 'change'}],
        'taxpayerIdentificationNum': [required('纳税人识别号'),
            {validator: validators.checkTaxpayerId, trigger: 'change'}],
        'establishDate': [
            {
                validator: (rule, value, callback) => {

                    let filed = '成立日期';
                    if (value == '') {
                        callback(new Error(filed + '不能为空'));
                    } else if (Object.prototype.toString.call(value) == '[object Date]' || typeof value == 'string') {
                        callback();
                    } else {
                        callback(new Error(filed + '格式不对'));
                    }
                },
                trigger: 'change'
            }
        ],
        'isLongCertificate': [required('证书是否长期')],
        'certificateExpiryDate': [],
        'organizationCategory': [required('组织机构类别')],
        'organizationCategoryDetail': [required('组织机构类别细分')],
        'enterpriseScale': [required('企业规模')],
        'registeredCapitalCurrency': [required('注册资本币种')],
        'registeredCapital': [
            amountValidator('注册资本', true)
        ],
        'organizationStatus': [required('机构状态')],
        'economicType': [required('经济类型')],
        'economicIndustryLargeClass': [required('经济行业大类')],
        'economicIndustrySmallClass': [required('经济行业小类')],
        'enterpriseBusinessScope': [required('经营（业务）范围'), {
            max: 1000,
            message: '填写经营（业务）范围超过1000字符',
            trigger: 'change'
        }],

        enterpriseRegistration_Region: [validators.regionValidator('企业注册地', 3)],
        'enterpriseRegistrationProvince': [required('企业注册地省份')],
        'enterpriseRegistrationCity': [required('企业注册地城市')],
        'enterpriseRegistrationDistrict': [required('企业注册地区/县')],
        'enterpriseRegistrationAddress': [validators.addressValidator('企业注册地详细地址', true)],

        enterpriseManagement_Region: [validators.regionValidator('企业经营地', 3)],
        'enterpriseManagementProvince': [required('企业经营地省份')],
        'enterpriseManagementCity': [required('企业经营地城市')],
        'enterpriseManagementDistrict': [required('企业经营地区/县')],
        'enterpriseManagementAddress': [validators.addressValidator('企业经营地详细地址', true)],

        'enterpriseContactPhone': [required('企业联系人电话'), {
            validator: validators.phone,
            trigger: 'change'
        }],
        "corporationName": [validators.custName('法人姓名')],
        "corporationCertificateType": [required('法人证件类型')],
        "corporationCertificateNo": [
            /*required('法人证件号码'), {
            validator: validators.certNo,
            trigger: 'change'
            }*/
        ],
        "corporationPhone": [required('法人手机号'), {
            validator: validators.mobile,
            trigger: 'change'
        }],
        "corporationShareholdingRatio": [
            pctValidator('持股比例', true)
        ],
    },
    //TODO : 亲属表单
    relativeRule: {
        "relativeType": [required('与申请人关系')], //
        "relativeName": [validators.custName('姓名')], //
        "relativeCertificateType": [required('证件类型')], //
        "relativeCertificateNo": [required('证件号码'), {
            validator: validators.certNo,
            trigger: 'change'
        }], //
        "relativePhone": [required('手机号码'), {
            validator: validators.mobile,
            trigger: 'change'
        }], //
        "relativeAnnualSalaryAfterTax": [amountValidator('税后年薪', true, 1000)], //
        "relativeCompanyName": [validators.headAndTailNotHasBlank('单位名称', true), {
            max: 100,
            message: '单位名称超过100字符',
            trigger: 'change'
        }], //

        relativeCompany_Region: [validators.regionValidator('单位', 3)],
        "relativeCompanyProvince": [required('单位所在省份')], //
        "relativeCompanyCity": [required('单位所在城市')], //
        "relativeCompanyDistrict": [required('单位所在区/县')], //
        "relativeCompanyAddress": [validators.addressValidator('单位详细地址', true)], //
    },
    //TODO : 联系人表单
    contactorRule: {
        'contactRelationship': [required('与申请人关系')],
        'contactName': [validators.custName('姓名')],
        'contactPhone': [required('电话'), {
            validator: validators.mobile,
            trigger: 'change'
        }],
    },
    //TODO : 个人担保人表单
    guarantorPersonalRule: {
        'guarantorRelationship': [required('与申请人关系')],
        'guarantorName': [validators.custName('担保人姓名')],
        'guarantorCredentialType': [required('证件类型')],
        'guarantorIdentityCard': [required('证件号码'), {
            validator: validators.certNo,
            trigger: 'change'
        }],
        'guarantorPhone': [required('手机号码'), {
            validator: validators.mobile,
            trigger: 'change'
        }],
        'companyName': [validators.headAndTailNotHasBlank('单位名称', true), {
            max: 100,
            message: '单位名称超过100字符',
            trigger: 'change'
        }],
        'companyPhone': [required('单位电话'), {
            validator: validators.phone,
            trigger: 'change'
        }],

        company_Region: [validators.regionValidator('单位所在', 3)],
        'companyProvince': [required('单位所在省份')],
        'companyCity': [required('单位所在城市')],
        'companyDistrict': [required('单位所在区/县')],
        'companyDetailAddress': [validators.addressValidator('单位详细地址', true)],

        currentResidence_Region: [validators.regionValidator('现居住地', 3)],
        'currentResidenceProvince': [required('现居住地省份')],
        'currentResidenceCity': [required('现居住地城市')],
        'currentResidenceDistrict': [required('现居住地区/县')],
        'currentResidenceAddress': [validators.addressValidator('现居住地详细地址', true)],

        censusRegister_Region: [validators.regionValidator('户籍地', 3)],
        'censusRegisterProvince': [required('户籍地省份')],
        'censusRegisterCity': [required('户籍地城市')],
        'censusRegisterDistrict': [required('户籍地区/县')],
        'censusRegisterAddress': [validators.addressValidator('户籍地详细地址', true)],
    },
    //TODO: 企业担保人表单
    guarantorEnterpriseRule: {
        'guarantorRelationship': [required('与申请人关系')],
        'enterpriseName': [validators.headAndTailNotHasBlank('企业名称', true), {
            max: 100,
            message: '公司名称超过100字符',
            trigger: 'change'
        }],
        'enterpriseNature': [required('企业性质')],
        'unifiedSocialCreditCode': [required('统一社会信用代码'), {
            validator: validators.isCreditCode,
            trigger: 'change'
        }],

        'corporationName': [validators.custName('法人姓名')],
        'corporationIdentityCard': [required('法人证件号'), {
            validator: validators.certNo,
            trigger: 'change'
        }],
        'corporationPhone': [required('法人联系电话'), {
            validator: validators.phone,
            trigger: 'change'
        }],
        'actualControllerName': [validators.custName('实际控制人姓名')],
        'actualControllerIdentityCard': [required('实际控制人证件号码'), {
            validator: validators.certNo,
            trigger: 'change'
        }],
        'actualControllerPhone': [required('实际控制人联系电话'), {
            validator: validators.phone,
            trigger: 'change'
        }],

        enterpriseRegistration_Region: [validators.regionValidator('企业注册地', 3)],
        'enterpriseRegistrationProvince': [required('企业注册地省份')],
        'enterpriseRegistrationCity': [required('企业注册地城市')],
        'enterpriseRegistrationDistrict': [required('企业注册地区/县')],
        'enterpriseRegistrationAddress': [validators.addressValidator('企业注册地详细地址', true)],
        enterpriseManagement_Region: [validators.regionValidator('企业经营地', 3)],
        'enterpriseManagementProvince': [required('企业经营地省份')],
        'enterpriseManagementCity': [required('企业经营地城市')],
        'enterpriseManagementDistrict': [required('企业经营地区/县')],
        'enterpriseManagementAddress': [validators.addressValidator('企业经营地详细地址', true)],
    },

}



