<template>
    <div>
        <!--联系人表单-->
        <div class="cm-panel" v-if="contactorArr && contactorArr.length">
            <h3 class="margin-b-14">联系人信息</h3>
            <div v-for="(item, index) in contactorArr" :key="index">
                <Form :ref="'contactor' + index" :model="item" :rules="contactorRule" :label-width="120">
                    <Row>
                        <i-col :xs="24" :sm="12" :md="8">
                            <FormItem label="与申请人关系" prop="contactRelationship" :rules="relationShipRule">
                                <i-select type="text" v-model="item.contactRelationship" placeholder="请选择"
                                          @on-change="val=>{ handleChange(val,'contactor.'+index+'.contactRelationship')}">
                                    <i-option v-for="(item,index) in dp['relationship']" :value="item.dictCode"
                                              :key="item.dictCode">
                                        {{item.dictName}}
                                    </i-option>
                                </i-select>
                            </FormItem>
                        </i-col>
                        <i-col :xs="24" :sm="12" :md="8">
                            <FormItem label="姓名" prop="contactName">
                                <Input clearable
                                       v-model="item.contactName" placeholder="请输入"
                                       :v="'contactor.'+index+'.contactName'"
                                       @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                            </FormItem>
                        </i-col>
                        <i-col :xs="24" :sm="12" :md="8">
                            <FormItem label="电话" prop="contactPhone">
                                <Input clearable
                                       v-model="item.contactPhone" placeholder="请输入"
                                       :v="'contactor.'+index+'.contactPhone'"
                                       @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                            </FormItem>
                        </i-col>
                    </Row>
                </Form>
            </div>
        </div>
    </div>
</template>
<script>
    /**
     * @Description: 联系人表单
     */
    import base from './js/custbase';
    import rules from './js/custRules';
    import {yx} from '@/utils/helper';

    export default {
        name: 'orderContactorForm',
        mixins: [base],
        props: {},
        components: {},
        data() {
            return {
                contactorRule: rules.contactorRule,
            }
        },
        computed: {
            //返回联系人数组
            contactorArr() {
                return this.orderData.cust.contactor;
            },
            //与申请人关系校验，写在data中时初始有时无法触发，所以写在这里
            relationShipRule() {
                return [
                    {
                        validator:
                            (rule, value, callback) => {
                                let item0 = this.contactorArr[0];
                                let item1 = this.contactorArr[1];
                                if (yx.isEmpty(value)) {
                                    callback(new Error('与申请人关系不能为空'))
                                } else if (!yx.isEmpty(item0.contactRelationship) && !yx.isEmpty(item1.contactRelationship) && (item0.contactRelationship === item1.contactRelationship)) {
                                    callback(new Error('联系人关系不能相同'))
                                } else {
                                    callback()
                                }
                            },
                        trigger: 'change'
                    }
                ]
            }
        },
        mounted() {
            this.queryDictData(['relationship']);
            this.$nextTick(() => {
                this.todoRecount();
            })
        },
        methods: {
            //校验当前表单，可由父页面调用
            validate(cb) {
                let arr = [];
                let refName = ['contactor0', 'contactor1'];
                refName.forEach(ref => {
                    arr.push(new Promise((resolve) => {
                        if (this.$refs[ref]) this.$refs[ref][0].validate((valid) => {
                            resolve(valid);
                        });
                        else resolve(true)
                    }))
                })
                Promise.all(arr).then((result) => {
                    var flag = _.every(result);
                    cb && cb(flag);
                });
            },

            //输入框失去焦点
            handleBlur(e) {
                let handleField = e.target.parentNode.getAttribute('v') || e.target.parentNode.parentNode.getAttribute('v');
                //判断值是否改变
                if (!this.checkFieldIsChange(handleField)) return false;
                this.$nextTick(() => {
                    this.cacheData('contactor');
                    this.todoRecount();
                })
            },
            //下拉框改变值
            handleChange(val, handleField) {
                //判断值是否改变
                if (!this.checkFieldIsChange(handleField)) return false;
                this.$nextTick(() => {
                    if (handleField == 'contactor.0.contactRelationship' && !yx.isEmpty(this.contactorArr[1].contactRelationship)) {
                        this.$refs['contactor1'][0].validateField('contactRelationship')
                    }
                    if (handleField == 'contactor.1.contactRelationship' && !yx.isEmpty(this.contactorArr[0].contactRelationship)) {
                        this.$refs['contactor0'][0].validateField('contactRelationship')
                    }
                    this.cacheData('contactor');
                    this.todoRecount();
                })
            },
        },
        watch: {}
    }
</script>



